import {Injectable} from '@angular/core';
import {cloneDeep} from 'lodash';
import {RepositoryService} from '../../api/repository.service';
import {Observable, of} from 'rxjs';
import {map} from 'rxjs/operators';
import {CustomBlock} from '../../../../common/entities/CustomBlock';
import {HtmlObject} from '../../../../common/entities/HtmlObject';
import {EditorEventListener} from '../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {AsyncValue} from '../../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../../tools/decorators/StateStream/StateStream.decorator';
import {ApplicationConfig} from '../../entities/ApplicationConfig';

// todo add to blocks state

@Injectable()
@EditorEventListener()
export class CustomBlocksService {

  // @ts-ignore
  @AsyncValue() @StateStream({stateProperty: 'config'}) public config: ApplicationConfig/* = this.config*/;
  customBlocks: CustomBlock[];
  documentId: string;

  constructor(private repository: RepositoryService) {
    this.documentId = window.location.pathname.replace('/', '') || '1';
  }

  public getCustomBlocks(): Observable<CustomBlock[]> {

    if (this.config.useFakeData) {
      this.customBlocks = [];
    }

    if (this.customBlocks) {
      return of(this.customBlocks);
    }

    return this.repository
      .loadBlocks(this.documentId)
      .pipe(
        map(blocks => {
          this.customBlocks = blocks;
          return this.customBlocks;
        })
      );
  }

  public saveCustomBlock(id: string, node: HtmlObject): void {
    const block: CustomBlock = cloneDeep({...node, customBlockId: id, path: null, documentId: this.documentId});
    this.customBlocks.push(block);
    this.repository
      .createBlock(block, this.documentId)
      .subscribe(() => console.log('block created'), e => console.error('failed to store block', e));
  }
}
