import {ChangeDetectorRef, Component, ElementRef, Injector, OnInit} from '@angular/core';
import {EventsService} from '../../../state/events.service';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {ActionsService} from '../../../domain/actions.service';
import {NodeWrapperService} from '../../../services/node-wrapper/node-wrapper.service';
import {CustomBlocksService} from '../../../services/custom-blocks/custom-blocks.service';
import {DocumentNodeComponent} from '../document-node-component/document-node.component';
import {ExternalBlock} from '../../../entities/ApplicationConfig';
import {cloneDeep} from 'lodash';
import {ConfigStateService} from '../../../state/config.state.service';

// import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';

@Component({
  selector: 'external-block-wrapper',
  templateUrl: './external-block-wrapper.component.html'
})
export class ExternalBlockWrapperComponent extends DocumentNodeComponent implements OnInit {
  // public Editor = ClassicEditor;

  externalBlock: ExternalBlock;

  constructor(el: ElementRef,
              events: EventsService,
              actionManager: ActionManagerService,
              cdr: ChangeDetectorRef,
              actions: ActionsService,
              nodeWrapper: NodeWrapperService,
              customBlocks: CustomBlocksService,
              private configState: ConfigStateService,
              injector: Injector) {
    super(el, events, actionManager, cdr, actions, nodeWrapper, customBlocks, injector);
  }

  // todo rerender on undo redo only
  ngOnInit() {
    super.ngOnInit();
    this.externalBlock = this.configState.getExternalBlockConfig(this.node.tag);
    !this.externalBlock.initialized && this.externalBlock.initialize(this.applicationState.previewFrame.contentWindow);
    this.externalBlock.render(this.el.nativeElement, cloneDeep(this.node), {
      onChange: node => {
        // this.actionManager.runActions([
        //   this.actions.getNodeUpdateAction(node),
        // ]);
        // this.cdr.detectChanges();
        console.log('UPDATED');
      }
    });
  }


}

