import {Injectable} from '@angular/core';
import {EventsService} from '../state/events.service';
import {DocumentSetEvent, EditorEventType} from '../entities/EditorEvent';
import {Patch} from '../../../../ui-editor-common/entities/Patch';
import {WSMessages} from '../../../../ui-editor-common/entities/WSMessages';
import {EventSubscriber, StateSubscriber} from '../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {AsyncValue} from '../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../tools/decorators/StateStream/StateStream.decorator';
import {ApplicationState} from '../state/state.types';
import {BehaviorableNode} from '../entities/BehaviorableNode';
import {ConfigStateService} from '../state/config.state.service';
import {DocumentStateService} from '../state/document.state.service';

@Injectable()
@EditorEventListener()
export class WebsocketService {
  @AsyncValue()
  @StateStream({stateProperty: 'applicationState'})
  // @ts-ignore
  private applicationState: ApplicationState/* = this.applicationState*/;
  private ws: WebSocket;

  // todo POC
  public connected: boolean;

  public constructor(private configState: ConfigStateService,
                     private documentState: DocumentStateService) {
    window['ws'] = this;
  }

  @EventSubscriber({type: EditorEventType.SET_DOCUMENT})
  private onDocumentSet(e: DocumentSetEvent): void {
    e.data && this.connectToPatchService(e.data.id);
  }

  @StateSubscriber({stateProperty: 'applicationState.selectedNode'})
  private onNodeSelected(node: BehaviorableNode) {
    this.sendWSMessage(WSMessages.NODE_SELECTED, {
      path: node ? node.path : null,
      // documentId: this.data.getDocumentId()
      documentId: this.documentState.getDocumentId()
    });
  }

  private onRemoteNodeSelect(data): void {
    EventsService.dispatch({
      type: EditorEventType.UPDATE_APP_STATE,
      data: {
        selectedRemoteNodes: [...this.applicationState.selectedRemoteNodes, {node: this.documentState.getChildByPath(data.path), ...data}]
      }
    });

  }

  private onRemotePatch(data) {
    console.log('onRemotePatch', data);
    EventsService.dispatch({type: EditorEventType.REMOTE_UPDATE, data});
  }

  public sendWSMessage(event: WSMessages, data: any): void {
    console.log('sendWSMessage', event, data);
    this.ws && this.ws.send(JSON.stringify({event, data}));
  }

  public savePatch(patch: Patch): void {
    console.log('savePatch:', patch);
    this.sendWSMessage(WSMessages.PATCH, patch);
    EventsService.dispatch({type: EditorEventType.USER_UPDATE, data: patch});
  }

  public connectToPatchService(documentId: any): void {
    const HOST = this.configState.getWSHost();
    this.ws = new WebSocket(HOST);

    this.ws.onopen = () => {
      this.sendWSMessage(WSMessages.CONNECT, {documentId});
      this.connected = true;
    };

    this.ws.onmessage = e => {
      console.log('got WS message:', e);
      try {
        const data = JSON.parse(e.data);
        this[data.type] && this[data.type](data.data);
      } catch (err) {
        console.warn('WS error:', err);
      }
    };

    this.ws.onclose = () => {
      console.log('closed');
      this.ws.close();
    };
  }

  public disconnect(): void {
    this.ws.close();
    this.connected = false;
  }
}
