import {BaseDomRenderer} from '../components/renderers/dom/base.dom-renderer';
import {Component, InjectionToken} from '@angular/core';
import {NodeObject} from '../../../common/entities/NodeObject';

/**
 * Interface компонента для відображення Node
 * містить в собі node {@link NodeObject} і контейнер в якому цей node відображається {@link BaseDomRenderer}
 */
export interface NodeComponent extends Component {
  node: NodeObject;
  container: BaseDomRenderer;
}

export const NodeComponentNode = new InjectionToken<number>('NodeComponentNode');
