import {Command, DeleteCommand, InsertCommand, MoveCommand} from '../../../common/entities/Command';
import {DataMethod} from '../../../common/services/dom.data.service';
import {NodeUtils} from '../../../common/tools/NodeUtils';
import {Path} from '../../../common/entities/Path';
import {Patch} from '../../../common/entities/Patch';
import {cloneDeep} from 'lodash';
import {Action} from '../../../common/entities/Action';

interface Transformable {
  command: Command;

  getPaths(): Path[];

  setPaths(paths: Path[]): void;
}

interface Transformer {
  transform(path: Path): Path;
}

class InsertDeleteUpdateTransformable implements Transformable {
  constructor(public command: InsertCommand | DeleteCommand) {
  }

  public getPaths(): Path[] {
    return [this.command.path];
  }

  public setPaths([path]: Path[]): void {
    this.command.path = path;
  }
}

class MoveTransformable implements Transformable {
  constructor(public command: MoveCommand) {
  }

  public getPaths(): Path[] {
    return [this.command.path, this.command.destinationPath];
  }

  public setPaths([path, destinationPath]: Path[]): void {
    this.command.path = path;
    this.command.destinationPath = destinationPath;
  }
}

class DeleteTransformer implements Transformer {
  constructor(public command: DeleteCommand) {
  }

  public transform(path: Path): Path {
    const dependency = NodeUtils.getStructuralDependency(path, this.command.path);
    let transformed = [...path];
    if (dependency === 0) {
      transformed = null;
    }
    if (dependency < 0) {
      transformed[this.command.path.length - 1]--;
    }
    return transformed;
  }
}

class InsertTransformer implements Transformer {
  constructor(public command: DeleteCommand) {
  }

  public transform(path: Path): Path {
    const dependency = NodeUtils.getStructuralDependency(path, this.command.path);
    const transformed = [...path];
    if (dependency <= 0) {
      transformed[this.command.path.length - 1]++;
    }
    return transformed;
  }
}

class MoveTransformer implements Transformer {
  constructor(public command: MoveCommand) {
  }

  public transform(path: Path): Path {
    const sourceDependency = NodeUtils.getStructuralDependency(path, this.command.path);
    const destinationDependency = NodeUtils.getStructuralDependency(path, this.command.destinationPath);

    let transformed = [...path];

    if (sourceDependency < 0) {
      transformed[this.command.path.length - 1]--;
    }

    // todo CHECK
    if (sourceDependency === 0) {
      // transformed = [...this.command.destinationPath, ...transformed.slice(this.command.destinationPath.length, transformed.length)];
      transformed = [...this.command.destinationPath, ...transformed.slice(this.command.path.length, transformed.length)];
    }

    if (destinationDependency <= 0) {
      transformed[this.command.destinationPath.length - 1]++;
    }

    return transformed;
  }
}

class Transformator {
  constructor(public transformable: Transformable, public transformer: Transformer) {
  }

  public transform() {
    const paths = this.transformable
      .getPaths()
      .map(p => {
        const path = this.transformer.transform(p);
        if (path === null) {
          this.transformable.command.detached = true;
          return p;
        }
        return path;
      });

    if (!this.transformable.command.detached) {
      this.transformable.setPaths(paths);
    }

    return this.transformable.command;
  }
}

export class OperationUtils {

  private static transformables = {
    [DataMethod.MOVE]: MoveTransformable,
    [DataMethod.INSERT]: InsertDeleteUpdateTransformable,
    [DataMethod.DELETE]: InsertDeleteUpdateTransformable,
    [DataMethod.UPDATE]: InsertDeleteUpdateTransformable,
  };

  private static transformers = {
    [DataMethod.MOVE]: MoveTransformer,
    [DataMethod.INSERT]: InsertTransformer,
    [DataMethod.DELETE]: DeleteTransformer,
  };

  public static transformAgainst(transformed: Command, target: Command): Command {
    console.log('\ntransforming', transformed);
    console.log('against', target);
    const transformedCommand = {...transformed, detached: false};

    if (OperationUtils.transformers[target.method]) {
      const transformator = new Transformator(
        // @ts-ignore
        new OperationUtils.transformables[transformedCommand.method](transformedCommand),
        new OperationUtils.transformers[target.method](target)
      );
      const t = transformator.transform();
      console.log('transformed:', {...t});
      return t;
    }

    console.log('transformed:', {...transformedCommand});
    return transformedCommand;
  }

  public static reverseActions(actions: Action[]): Action[] {
    return [...actions].reverse().map(a => ({do: a.undo, undo: a.do}));
  }


  public static transformActions(transformed: Action[], targetActionsArray: Action[][]): Action[] {
    console.log('transformActions', transformed, targetActionsArray);
    const transformedActions: Action[] = cloneDeep(transformed);
    targetActionsArray.forEach(targetActions => {
      targetActions
        .forEach(targetAction => {
          transformedActions
            .forEach(transformedAction => {
              transformedAction.do = OperationUtils.transformAgainst(transformedAction.do, targetAction.do);
              transformedAction.undo = OperationUtils.transformAgainst(transformedAction.undo, targetAction.do);
            });
        });
    });

    // todo ???
    // cancelActions
    //   .forEach(cancelAction => {
    //     cancelAction.undo = OperationUtils.transformAgainst(cancelAction.undo, cancelAction.do);
    //   });

    return transformedActions;
  }
}
