import {Injectable} from '@angular/core';
import {ControlObject, SettingsObject} from '../../entities/UIObject';
import {baseBlockControls} from '../../configs/baseBlocks';
import {cloneDeep} from 'lodash';
import {Observable, of} from 'rxjs';
import {Control} from '../../../../common/entities/Control';
import {RepositoryService} from '../../api/repository.service';
import {map} from 'rxjs/operators';
import {AsyncValue} from '../../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../../tools/decorators/StateStream/StateStream.decorator';
import {ApplicationConfig} from '../../entities/ApplicationConfig';
import {EditorEventListener} from '../../tools/decorators/EditorEventListener/EditorEventListener.decorator';

// todo add to controls state to config
/**
 * Сервіс, що зберігає контроли і віддає потрібні контроли для заданного блоку
 */
@Injectable()
@EditorEventListener()
export class ControlsService {
  documentId: string;
  controls: any;
  // @ts-ignore
  @AsyncValue() @StateStream({stateProperty: 'config'}) public config: ApplicationConfig/* = this.config*/;

  constructor(private repository: RepositoryService) {
    this.documentId = window.location.pathname.replace('/', '') || '1';
  }

  private loadControls(): Observable<any> {
    if (this.config.useFakeData) {
      this.controls = {
        undefined: baseBlockControls,
      };
    }

    if (this.controls) {
      return of(this.controls);
    }

    return this.repository
      .loadControls(this.documentId)
      .pipe(
        map(controls => {
          console.log("got controls from server:", controls);
          this.controls = {
            undefined: baseBlockControls,
          };
          controls.forEach(c => {
            if (!this.controls[c.customBlockId]) {
              this.controls[c.customBlockId] = [];
            }
            this.controls[c.customBlockId].push(c);
          });
          return this.controls;
        })
      );
  }

  public saveControl(control: SettingsObject, blockId?: string): void {
    if (!this.controls[blockId]) {
      this.controls[blockId] = [];
    }
    if (control.id) {
      const storedControl = this.controls[blockId].find(c => c.id === control.id);
      Object.assign(storedControl, control);
      this.repository
        .updateControl(storedControl, this.documentId)
        .subscribe(() => console.log('control updated'), e => console.error('failed to update control', e));
    } else {
      const newControl = {
        ...control,
        id: `control_${(new Date()).getTime()}`,
        customBlockId: blockId,
        documentId: this.documentId
      } as Control;
      this.controls[blockId].push(newControl);
      this.repository
        .createControl(newControl, this.documentId)
        .subscribe(() => console.log('control created'), e => console.error('failed to create control', e));
    }
  }

  public getControls(customBlockId: string): Observable<ControlObject[]> {
    return this
      .loadControls()
      .pipe(
        map(() => {
          console.log("getting controls for:", customBlockId);
          console.log("controls map:", this.controls)
          let controls = this.controls[customBlockId];
          if (!controls) {
            controls = [];
            this.controls[customBlockId] = controls;
          }
          return cloneDeep(controls);
        })
      );
  }
}
