import {BaseValueAccessor} from './BaseValueAccessor';
import {get, set} from 'lodash';

export class BufferValueAccessor extends BaseValueAccessor {

  private value;

  // public transformer = true; // ???

  constructor(value: any = {}) {
    super();
    this.value = value;
  }

  getRawValue(): any {
    return this.value;
  }

  getValue(valuePath: string): any {
    if (valuePath) {
      return get(this.value, valuePath);
    }
    return this.value;
  }

  setValue(valuePath: string, value: any): any {
    if (valuePath) {
      set(this.value, valuePath, value);
      return;
    }
    this.value = value;
  }

}
