import {ControlValueAccessor} from '../../../../entities/UIObject';

export abstract class BaseValueAccessor implements ControlValueAccessor {
  public transformer = false;
  public structural = false;

  abstract getRawValue(): any ;

  abstract getValue(valuePath: string): any;

  abstract setValue(valuePath: string, value: any): any;

  isValid() {
    return true;
  }

  getModifiedValue() {
    return this.getRawValue();
  }
}
