import {ComponentFactoryResolver, Injector, Input, OnDestroy, OnInit, Type, ViewContainerRef} from '@angular/core';
import {BaseNodeComponent} from './base.node-component';
import {HtmlObject} from '../../../../../../ui-editor-common/entities/HtmlObject';
import {NodeComponentNode} from '../../../entities/NodeComponent';
import {NodeRenderer} from '../../../entities/NodeRenderer';

/**
 * Відображає node типу {@link HtmlObject}, створює компонент типу {@link BaseNodeComponent}
 */
export class BaseDomRenderer implements OnInit, OnDestroy, NodeRenderer {
  @Input() index: number;
  @Input() node: HtmlObject;
  componentRef: any;
  component: BaseNodeComponent;

  constructor(protected injector: Injector,
              protected componentFactoryResolver: ComponentFactoryResolver,
              protected viewContainerRef: ViewContainerRef) {
  }

  protected getComponentType(): Type<BaseNodeComponent> {
    return null;
  }

  protected insertComponent(): void {
    const injector = Injector.create({
      providers: [
        {provide: NodeComponentNode, useValue: this.node},
        {provide: 'dynamicallyProvidedFor', useValue: this.getComponentType().name}
      ],
      parent: this.injector
    });
    const factory = this.componentFactoryResolver.resolveComponentFactory(this.getComponentType());
    this.componentRef = this.viewContainerRef.createComponent(factory, 0, injector);
    this.component = this.componentRef.instance;
    this.component.index = this.index;
    this.component.container = this;
  }

  protected removeComponent(): void {
    this.viewContainerRef.detach(0);
    this.componentRef.destroy();
    delete this.componentRef;
    delete this.component;
  }

  ngOnInit(): void {
    this.insertComponent();
  }

  ngOnDestroy(): void {
    this.removeComponent();
  }

  public updateComponent(): void {
    this.removeComponent();
    this.insertComponent();
  }
}
