import {destroyEmitter, EmitterOptions} from '../decorator.types';
import {EventsService} from '../../../state/events.service';
import {isObservable} from 'rxjs';
import {takeUntil} from 'rxjs/operators';


function emit(options: EmitterOptions, data?: any, error?: any) {
  EventsService.dispatch({type: options.type, data, error});
}

export function Emitter(options: EmitterOptions) {
  return function (target: any, key: string, descriptor: PropertyDescriptor) {
    const originalMethod = descriptor.value;
    descriptor.value = function (...params) {
      const result = originalMethod.call(this, ...params);
      if (isObservable(result)) {
        const subscription = result
          .pipe(takeUntil(this[destroyEmitter]))
          .subscribe(
            r => {
              emit(options, r);
              if (options.unsubscribeImmediately) {
                subscription.unsubscribe();
              }
            },
            e => emit(options, null, e)
          );
      } else {
        emit(options, result);
      }
      return result;
    };
    return descriptor;
  };
}
