export default class DecoratorTools {

  public static defineProperty(target, key) {

    const valueKey = '_' + key;
    const getter = function () {
      const value = this[valueKey];
      return value;
    };

    const setter = function (newValue) {
      this[valueKey] = newValue;
    };

    Object.defineProperty(target, key, {
      get: getter,
      set: setter,
      enumerable: true,
      configurable: true
    });

    return Object.getOwnPropertyDescriptor(target, key);
  }

  public static getPropertyDescriptor(target: Object, key: string): PropertyDescriptor {
    let propertyDescriptor = Object.getOwnPropertyDescriptor(target, key);
    if (!propertyDescriptor) {
      propertyDescriptor = DecoratorTools.defineProperty(target, key);
    }
    return propertyDescriptor;
  }

  public static getMethodDescriptor(target: Object, key: string): PropertyDescriptor {
    let methodDescriptor = Object.getOwnPropertyDescriptor(target, key);
    // if (!methodDescriptor) {
    //   methodDescriptor = DecoratorTools.defineMethod(target, key);
    // }
    return methodDescriptor;
  }

  public static redefineAccessors(target, key, newAccessorsGetter) {
    const propertyDescriptor = DecoratorTools.getPropertyDescriptor(target, key);
    const _get = propertyDescriptor.get;
    const _set = propertyDescriptor.set;

    Object.defineProperty(target, key, newAccessorsGetter(_get, _set));
  }

  public static redefineObjectMethod(target, methodName, newMethodGetter): void {
    const method = target[methodName];
    target[methodName] = newMethodGetter(method);
  }

  public static customExtend(derivedCtor: any, baseCtors: any[]) {
    for (let i = 0, len = baseCtors.length; i < len; i++) {
      const baseCtor = baseCtors[i];
      const propertyKeys = Object.getOwnPropertyNames(baseCtor.prototype);
      for (let j = 0, len2 = propertyKeys.length; j < len2; j++) {
        const name = propertyKeys[j];
        derivedCtor.prototype[name] = baseCtor.prototype[name];
      }
    }
  }

  public static decorateConstructor(original, cb) {
    const decorated = new Proxy(original, {
      construct(clss, params, newTarget) {
        // console.log('A1:', arguments);
        const doConstruct = () => Reflect.construct(clss, params, newTarget);
        return cb(doConstruct, params);
      },
      apply(target, thisArg, params) {
        // console.log('A2', arguments);
        // console.log('thisArg', thisArg);
        const doCall = () => target.call(thisArg, ...params);
        if (target === original) {
          return cb(doCall, params, thisArg);
        }
        return doCall();
      }
    });

    const metaDataKeys = Reflect['getMetadataKeys'](original);
    metaDataKeys.forEach(function (key) {
      Reflect['defineMetadata'](key, Reflect['getOwnMetadata'](key, original), decorated);
    });

    return decorated;
  }
}
