import {
  ControlObject,
  ControlType,
  ControlUpdateType,
  ControlValueAccessorType,
  InputObject,
  InputType
} from '../entities/UIObject';

export class ControlUtils {

  static readonly valueAccessorProp = 'valueAccessor';
  static readonly editedNodeProp = 'editedNode';

  static readonly defaultUINodeProperties = {
    type: 1,
    children: [],
    path: [],
    localState: {},
    attributes: {}
  };

  static readonly defaultControlProperties = {
    ...ControlUtils.defaultUINodeProperties,
    attributes: {style: 'display: block;'},
    updateType: ControlUpdateType.MODIFY
  };

  static getValueAccessorProperty(property: string): string {
    return property.replace(new RegExp(`${ControlUtils.valueAccessorProp}(.?)`), '');
  }

  static getLabel(options: { value?: string, bindTo?: string }): InputObject {
    return {
      ...ControlUtils.defaultControlProperties,
      tag: InputType.LABEL,
      value: options.value,
      bindings: {value: options.bindTo},
      localState: {initialValue: options.value},
    };
  }

  static getInput(options: {
    type?: InputType,
    bindTo: string,
  }): InputObject {
    return {
      ...ControlUtils.defaultControlProperties,
      tag: options.type || InputType.TEXT,
      bindings: {value: options.bindTo},
    };
  }

  static getBasicStyleControl(options: {
    id: string;
    bindTo: string,
    label: string,
    styleProperty: string,
    inputType?: InputType,
  }): ControlObject {
    return {
      ...ControlUtils.defaultControlProperties,
      id: 'styleControl_' + options.id,
      tag: ControlType.BASE,
      valueAccessor: ControlValueAccessorType.STYLE,
      bindings: {value: options.bindTo},
      children: [
        ControlUtils.getLabel({value: options.label}),
        ControlUtils.getInput({
          bindTo: `${ControlUtils.valueAccessorProp}.${options.styleProperty}`,
          type: options.inputType || InputType.TEXT
        })
      ],
    };
  }

  static getTextControl(options: {
    id: string;
    bindTo: string,
    label?: string,
    inputType?: InputType,
  }): ControlObject {
    return {
      ...ControlUtils.defaultControlProperties,
      id: 'textControl_' + options.id,
      tag: ControlType.BASE,
      valueAccessor: ControlValueAccessorType.TEXT,
      bindings: {value: options.bindTo},
      children: [
        ControlUtils.getLabel({value: options.label || 'Text'}),
        ControlUtils.getInput({bindTo: `${ControlUtils.valueAccessorProp}`, type: InputType.TEXT})
      ],
    };
  }

  static getFormControl(options: {
    id: string;
    bindTo?: string,
    iterate?: string,
    updateType?: ControlUpdateType,
  }): ControlObject {
    return {
      ...ControlUtils.defaultControlProperties,
      id: 'formControl_' + options.id,
      tag: ControlType.BASE,
      valueAccessor: ControlValueAccessorType.FORM,
      bindings: {value: options.bindTo},
      iterate: options.iterate,
      updateType: options.updateType,
      children: [
        options.updateType === ControlUpdateType.INSERT ?
          ControlUtils.getInput({bindTo: `${ControlUtils.valueAccessorProp}.key`}) :
          ControlUtils.getLabel({bindTo: `${ControlUtils.valueAccessorProp}.key`}),
        ControlUtils.getInput({bindTo: `${ControlUtils.valueAccessorProp}.value`}),
      ],
    };
  }

  static getButton(options: { value?: any, bindTo?: string, label: string }): InputObject {
    return {
      ...ControlUtils.defaultControlProperties,
      tag: InputType.BUTTON,
      value: options.value,
      bindings: {value: options.bindTo},
      localState: {label: options.label, initialValue: options.value}
    };
  }

  static isBoundToValueAccessor(binding: string): boolean {
    return binding.indexOf(ControlUtils.valueAccessorProp) === 0;
  }

  static getModifiedProperty(property: string = ''): string {
    let modifiedProperty = property
      .replace(ControlUtils.valueAccessorProp, '')
      .replace(ControlUtils.editedNodeProp, '');

    if (modifiedProperty.charAt(0) === '.') {
      modifiedProperty = modifiedProperty.slice(1);
    }

    return modifiedProperty;
  }
}
