import {ChangeDetectorRef, Component, ElementRef, Injector, OnInit} from '@angular/core';
import {HtmlConverter} from '../../../tools/HtmlConverter';
import {DomSanitizer, SafeHtml} from '@angular/platform-browser';
import {EventsService} from '../../../state/events.service';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {ActionsService} from '../../../domain/actions.service';
import {NodeWrapperService} from '../../../services/node-wrapper/node-wrapper.service';
import {CustomBlocksService} from '../../../services/custom-blocks/custom-blocks.service';
import {HtmlObject} from '../../../../../../ui-editor-common/entities/HtmlObject';
import {DocumentNodeComponent} from '../document-node-component/document-node.component';

@Component({
  selector: 'amp-element',
  templateUrl: './amp-element.component.html',
  styleUrls: ['../document-node-component/document-node.component.less', './/amp-element.component.less'],
})
export class AmpElementComponent extends DocumentNodeComponent implements OnInit {

  converter = HtmlConverter;

  children: any[] = [];
  play: boolean = true;

  constructor(el: ElementRef,
              events: EventsService,
              actionManager: ActionManagerService,
              cdr: ChangeDetectorRef,
              actions: ActionsService,
              nodeWrapper: NodeWrapperService,
              customBlocks: CustomBlocksService,
              private sanitizer: DomSanitizer,
              injector: Injector) {
    super(el, events, actionManager, cdr, actions, nodeWrapper, customBlocks, injector);
  }

  ngOnInit() {
    super.ngOnInit();
    this.children = this.node.children.map(c => this.getHtml(c));
  }

  public getHtml(object: HtmlObject): SafeHtml {
    const html = HtmlConverter.ObjectToHtml(object);
    return html ? this.sanitizer.bypassSecurityTrustHtml(html) : '';
  }

  public playAMP(): void {
    this.play = !this.play;
    if (this.play) {
      this.children = this.node.children.map(c => this.getHtml(c));
    }
  }
}
