import {HtmlDocument} from '../../../common/entities/HtmlDocument';
import {ApplicationConfig} from './ApplicationConfig';
import {UIObject} from './UIObject';
import {ApplicationState} from '../state/state.types';
import {Patch} from '../../../common/entities/Patch';

export enum EditorEventType {
  SET_DOCUMENT = 'setDocument',
  SET_CONFIG = 'setConfig',
  SET_UI = 'setUI',
  SET_APP_STATE = 'setAppState',
  UPDATE_APP_STATE = 'updateAppState',
  MOUSE_OVER_NODE = 'mouseOverNode',
  NODE_DRAG_START = 'nodeDragStart',
  NODE_DRAG_END = 'nodeDragEnd',
  NODE_DRAG_ENTER = 'nodeDragEnter',
  NODE_DRAG_LEAVE = 'nodeDragLeave',
  /** @deprecated - causes very slow performance*/
  NODE_DRAG_OVER = 'nodeDragOver',
  NODE_DRAG_DROP = 'nodeDragDrop',
  MOUSE_OUT_NODE = 'mouseOutNode',
  /** @deprecated */
  NODE_SELECTED = 'nodeSelected',
  /** @deprecated */
  REMOTE_NODE_SELECTED = 'remoteNodeSelected',
  /** @deprecated */
  DATA_UPDATED = 'dataUpdated',
  REMOTE_UPDATE = 'remoteUpdate',
  USER_UPDATE = 'userUpdate',
  DO = 'do',
  UNDO = 'undo',
  UPDATE_DOCUMENT_NODE = 'updateDocumentNode',
  EXECUTE_PATCH = 'executePatch'
}

export interface EditorEvent {
  type: EditorEventType;
  data?: any;
  error?: any;
}

export interface DocumentSetEvent {
  data: HtmlDocument;
}

export interface ConfigSetEvent {
  data: ApplicationConfig;
}

export interface UISetEvent {
  data: UIObject;
}

export interface AppStateSetEvent {
  data: ApplicationState;
}

export interface AppStateUpdateEvent {
  data: Partial<ApplicationState>;
}

export interface PatchEvent {
  data: Patch;
}
