import {Component, EventEmitter, Input, Output, SimpleChanges} from '@angular/core';
import {HtmlConverter} from '../../../../tools/HtmlConverter';
import {HtmlObject} from '../../../../../../../ui-editor-common/entities/HtmlObject';
import {NodeRenderer} from '../../../../entities/NodeRenderer';


/**
 * Відповідає за відображення html коду документу (email)
 * <br/>Відображає node типу {@link HtmlObject}
 * <br/>На зміну значення емітить івент valueChanged з массивом нових {@link HtmlObject}
 */
@Component({
  selector: 'html-editor',
  templateUrl: './html-editor.component.html',
  styleUrls: ['./html-editor.component.less']
})
export class HtmlEditorComponent implements NodeRenderer {
  @Input() node: HtmlObject;
  @Output() valueChanged = new EventEmitter();
  html: string;

  protected generateCode() {
    this.html = HtmlConverter.ObjectToHtml(this.node);
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.generateCode();
  }

  protected generateNodes(html: string): HtmlObject[] {
    return HtmlConverter.HtmlToObject(html, this.node.path);
  }
}
