import {Injectable} from '@angular/core';
import {ApplicationConfig, ExternalBlock} from '../entities/ApplicationConfig';
import {AsyncValue} from '../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../tools/decorators/StateStream/StateStream.decorator';
import {ConfigSetEvent, EditorEventType} from '../entities/EditorEvent';
import {EventsService} from './events.service';
import {Reducer} from '../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorState} from './state.types';
import {EditorEventListener} from '../tools/decorators/EditorEventListener/EditorEventListener.decorator';

@Injectable()
@EditorEventListener()
export class ConfigStateService {
  @AsyncValue()
  @StateStream({stateProperty: 'config'})
  // @ts-ignore
  public config: ApplicationConfig/* = this.config*/;

  public constructor() {
    console.log('config state:', this);
  }

  @Reducer({type: EditorEventType.SET_CONFIG, stateProperty: 'config'})
  private onConfigSet(state: EditorState, e: ConfigSetEvent): ApplicationConfig {
    return e.data;
  }

  public setConfig(config: ApplicationConfig): void {
    EventsService.dispatch({type: EditorEventType.SET_CONFIG, data: config});
  }

  public getWSHost() {
    return `ws://${location.hostname}:8080`;
  }

  public getExternalBlocks(): ExternalBlock[] {
    return this.config.externalBlocks;
  }

  public getExternalBlockConfig(tag: string): ExternalBlock {
    return this.getExternalBlocks().find(b => b.defaultTemplate.tag === tag);
  }

  public getConfig(): ApplicationConfig {
    return this.config;
  }
}
