import {ChangeDetectorRef, Component, ElementRef, Injector, OnInit} from '@angular/core';
import {EventsService} from '../../../state/events.service';
import {BaseNodesContainerComponent} from '../base-nodes-container/base-nodes-container.component';
import {ControlUtils} from '../../../tools/ControlUtils';
import {BaseNodes} from '../../../entities/BaseNodes';
import {UIObject, UIObjectType} from '../../../entities/UIObject';
import {CustomBlocksService} from '../../../services/custom-blocks/custom-blocks.service';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {ConfigStateService} from '../../../state/config.state.service';

/**
 * Компонент для відображення стандартних блоків, external а також custom блоків,
 * потомок {@link BaseNodesContainerComponent}
 *
*/
@Component({
  selector: 'blocks-panel-component',
  // templateUrl: './blocks-panel.component.html',
  templateUrl: '../ui-node-component/ui-node.component.html',
  styleUrls: ['./blocks-panel.component.less']
})
@EditorEventListener()
export class BlocksPanelComponent extends BaseNodesContainerComponent implements OnInit {

  public constructor(el: ElementRef,
                     cdr: ChangeDetectorRef,
                     events: EventsService,
                     private customBlocks: CustomBlocksService,
                     private configState: ConfigStateService,
                     injector: Injector) {
    super(cdr, el, events, injector);
  }

  private getExternalBlocks(): UIObject[] {
    return this.configState.getExternalBlocks().map(b => ({
      ...ControlUtils.defaultUINodeProperties,
      tag: UIObjectType.BLOCK_THUMB,
      localState: {
        externalBlock: true,
        block: {...ControlUtils.defaultUINodeProperties, ...b.defaultTemplate, externalBlock: true}
      }
    }));
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.customBlocks
      .getCustomBlocks()
      .subscribe(customBlocks => {
        this.node.children = [
          ...Object
            .entries(BaseNodes)
            .map(([key, value]) => ({
              ...ControlUtils.defaultUINodeProperties,
              tag: UIObjectType.BLOCK_THUMB,
              localState: {
                block: {tag: value}
              }
            })),

          ...this.getExternalBlocks(),

          ...customBlocks
            .map(node => ({
              ...ControlUtils.defaultUINodeProperties,
              tag: UIObjectType.BLOCK_THUMB,
              localState: {
                customBlock: true,
                block: node
              }
            }))
        ];

        console.log('blocks:', this.node.children);
      });

  }
}
