import {ComparisonType, ConditionConnection} from '../entities/UIObject';

export class LogicalOperator {
  static [ComparisonType.EQUAL](a, b): boolean {
    return a === b;
  }

  static [ComparisonType.NOT_EQUAL](a, b): boolean {
    return a !== b;
  }

  static [ComparisonType.CAST_EQUAL](a, b): boolean {
    // tslint:disable-next-line:triple-equals
    return a == b;
  }

  static [ConditionConnection.AND](a, b): boolean {
    return a && b;
  }

  static [ConditionConnection.OR](a, b): boolean {
    return a || b;
  }
}
