import {Injectable} from '@angular/core';
import {EditorEventListener} from '../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {AsyncValue} from '../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../tools/decorators/StateStream/StateStream.decorator';
import {EventsService} from './events.service';
import {Reducer} from '../tools/decorators/Subscriber/Subscriber.decorator';
import {AppStateSetEvent, AppStateUpdateEvent, EditorEventType} from '../entities/EditorEvent';
import {ApplicationState} from './state.types';

// todo create base state class
@Injectable()
@EditorEventListener()
export class AppStateService {
  @AsyncValue()
  @StateStream({stateProperty: 'applicationState'})
  // @ts-ignore
  public state: ApplicationState/* = this.state*/;

  public constructor() {
    console.log('APP state:', this);
  }

  @Reducer({type: EditorEventType.SET_APP_STATE, stateProperty: 'applicationState'})
  private onSet(state: ApplicationState, e: AppStateSetEvent): ApplicationState {
    return e.data;
  }

  @Reducer({type: EditorEventType.UPDATE_APP_STATE, stateProperty: 'applicationState'})
  private onUpdate(state: ApplicationState, e: AppStateUpdateEvent): ApplicationState {
    return {...state, ...e.data};
  }

  public set(state: ApplicationState): void {
    EventsService.dispatch({type: EditorEventType.SET_APP_STATE, data: state});
  }
}
