import DecoratorTools from '../decorator.tools';
import {ExtraModuleInjector} from '../../../services/extra-module-injector/extra-module-injector';
import {InjectedValueOptions} from '../decorator.types';

export function InjectedValue(options: InjectedValueOptions) {
  return function (target: Object, key: string) {
    DecoratorTools.redefineAccessors(target, key, (_get, _set) => {
      return {
        get: function () {
          let value = _get.call(this);
          const useInjector = !options.dynamicInjector || this.constructor.name === this.injector.get('dynamicallyProvidedFor');
          if (useInjector && (!value /*|| !this.hasOwnProperty(key)*/)) {
            value = this.injector ? this.injector.get(options.token) : ExtraModuleInjector.get(options.token);
            _set.call(this, value);
          }
          return value;
        }
      };
    });
  };
}
