import {ControlUtils} from '../../../tools/ControlUtils';
import {UINodeComponent} from '../ui-node-component/ui-node.component';
import {set} from 'lodash';
import {ControlUpdateType, SettingsObject, UINodeEventType} from '../../../entities/UIObject';
import {EditorEventType} from '../../../entities/EditorEvent';
import {DeleteCommand} from '../../../../../../ui-editor-common/entities/Command';

/**
 * Базовий контрол редагування налаштувань підключається через декоратор Mixins
 */
export abstract class BaseControlEditSettings extends UINodeComponent {
  _value;
  node: SettingsObject;

  public abstract saveControl();

  /**
   * Встановлення прив'язки
   * @param prop ім'я параметру
   * @param newParentProp ім'я батьківского параметру, до якого прив'язується
   */
  public renameParentBindingProperty(prop: string, newParentProp: string): void {
    const prefix = !this.node.path.length ? 'editedNode' : ControlUtils.valueAccessorProp;
    newParentProp = newParentProp ? `${prefix}.${newParentProp}` : prefix;
    this.node.bindings[prop] = newParentProp;
    set(this.node, prop, undefined);
    this.saveControl();
  }

  public deleteControl(): void {
    this.applicationState.controlsDataService.removeNodes({path: this.node.path, count: 1} as DeleteCommand);
    this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
  }

  public getParentBindingPropertyName(property: string) {
    return !property ? property : property
      .replace(new RegExp(`editedNode(.?)`), '')
      .replace(new RegExp(`${ControlUtils.valueAccessorProp}(.?)`), '');
  }

  /**
   * Перейменування прив'язаного проперті
   * @param oldProp стара назва
   * @param newProp нова назва
   */
  public renameBindingProperty(oldProp: string, newProp: string): void {
    this.node.bindings[newProp] = this.node.bindings[oldProp];
    delete this.node.bindings[oldProp];
    set(this.node, oldProp, undefined);
    this.saveControl();
  }

  protected onDragStop(): void {
    super.onDragStop();
    this.cdr.markForCheck();
  }

  protected onDragStart() {
    setTimeout(() => {
      this.dragged = true;
      this.cdr.markForCheck();
    });
    this.applicationState = {draggedControl: this.node};
  }

  public setUpdateType(type: ControlUpdateType): void {
    this.node.updateType = type;
    this.saveControl();
  }

  public setLocalState(state: string): void {
    let stateObj: any;
    try {
      stateObj = JSON.parse(state);
    } catch (e) {
      console.error('Invalid config!');
      return;
    }

    this.node.localState = stateObj;
    this.saveControl();
  }

  public applyBindings(child: SettingsObject, context: any = this): SettingsObject {
    const result: SettingsObject = super.applyBindings(child, context);
    if (result.localState && result.localState.initialValue) {
      result.value = result.localState.initialValue;
    }
    return result;
  }

  protected updateLocalState() {
    super.updateLocalState();
    if (this.node.localState && this.node.localState.initialValue) {
      this.node.value = this.node.localState.initialValue;
      this._value = this.node.value;
    }
  }
}
