import {Injectable} from '@angular/core';
import {
    Action,
    DeleteAction,
    InsertAction,
    MoveAction,
    UpdateAction
} from '../../../../ui-editor-common/entities/Action';
import {HtmlObject} from '../../../../ui-editor-common/entities/HtmlObject';
import {DataMethod} from '../../../../ui-editor-common/services/dom.data.service';
import {UpdateSummary} from '../entities/UIObject';
import {ControlUtils} from '../tools/ControlUtils';
import {Path} from '../../../../ui-editor-common/entities/Path';
import {cloneDeep, get} from 'lodash';
import {DocumentStateService} from '../state/document.state.service';

@Injectable()
export class ActionsService {
    // todo maybe all objects should be deep cloned, investigate
    constructor(private documentState: DocumentStateService) {

    }

    public getNodeUpdateAction(oldNode: HtmlObject, modifiedPath: Path, modifiedProperty: string, modifiedValue: any): UpdateAction {
        return {
            do: {
                path: [...modifiedPath],
                method: DataMethod.UPDATE,
                attribute: modifiedProperty,
                value: cloneDeep(modifiedValue),
            },
            undo: {
                path: [...modifiedPath],
                method: DataMethod.UPDATE,
                attribute: modifiedProperty,
                value: cloneDeep(modifiedProperty ? get(oldNode, modifiedProperty) : oldNode),
            }
        };
    }

    public getNodeDeleteAction(path: Path, nodes: HtmlObject[]): DeleteAction {
        return {
            do: {
                path: [...path],
                count: nodes.length,
                method: DataMethod.DELETE
            },
            undo: {
                path: [...path],
                nodes: cloneDeep(nodes),
                method: DataMethod.INSERT
            }
        };
    }

    public getNodeInsertAction(path: Path, nodes: HtmlObject[]): InsertAction {
        return {
            do: {
                path: [...path],
                nodes: cloneDeep(nodes),
                method: DataMethod.INSERT
            },
            undo: {
                path: [...path],
                count: nodes.length,
                method: DataMethod.DELETE
            }
        };
    }

    public getNodeMoveAction(from: Path, to: Path): MoveAction {
        return {
            do: {
                path: [...from],
                method: DataMethod.MOVE,
                destinationPath: [...to]
            },
            undo: {
                path: [...to],
                method: DataMethod.MOVE,
                destinationPath: [...from]
            }
        };
    }

    public getActionBySummary(previousNodeValue: HtmlObject, summary: UpdateSummary): Action {
        if (summary.dataMethod === DataMethod.UPDATE) {
            const modifiedProperty = ControlUtils.getModifiedProperty(summary.modifiedProperty);
            const oldNode = summary.modifiedPath ? this.documentState.getChildByPath(summary.modifiedPath) : previousNodeValue;
            return this.getNodeUpdateAction(oldNode, summary.modifiedPath || oldNode.path, modifiedProperty, summary.value);
        } else if (summary.dataMethod === DataMethod.DELETE) {

        } else if (summary.dataMethod === DataMethod.INSERT) {

        }
        return null;
    }

}
