import {HtmlDocument} from '../entities/HtmlDocument';
import {HtmlObject} from '../entities/HtmlObject';
import {NodeUtils} from '../tools/NodeUtils';
import {DeleteCommand, InsertCommand, MoveCommand, UpdateCommand} from '../entities/Command';

export enum DataMethod {
  UPDATE = 'updateNode',
  DELETE = 'removeNodes',
  INSERT = 'insertNodes',
  MOVE = 'moveNodes'
}

/** @deprecated used for editing controls and in service
 * @todo unify with data state and inherit by it*/
export class DomDataService {

  public data: HtmlDocument;

  public constructor(data: HtmlDocument) {
    this.data = data;
  }

  public updateNode(command: UpdateCommand): HtmlObject {
    if (command.path && command.path.length) {
      return NodeUtils.updateNode(this.data.html, command.path, command.attribute, command.value);
    }

    this.data.html = command.value;
    return command.value;

  }

  public moveNodes(command: MoveCommand): HtmlObject[] {
    const removedNodes = NodeUtils.removeNodes(this.data.html, command.path, 1);
    return NodeUtils.insertNodes(this.data.html, command.destinationPath, removedNodes);
  }

  public insertNodes(command: InsertCommand): HtmlObject[] {
    return NodeUtils.insertNodes(this.data.html, command.path, command.nodes);
  }

  public removeNodes(command: DeleteCommand): HtmlObject[] {
    return NodeUtils.removeNodes(this.data.html, command.path, command.count);
  }


}
