import {HtmlObject} from '../../../common/entities/HtmlObject';
import {Path} from '../../../common/entities/Path';
import {DataMethod} from '../../../common/services/dom.data.service';

export enum UIObjectType {
  CONTAINER = 'CONTAINER',
  PREVIEW = 'PREVIEW',
  CODE_EDITOR = 'CODE_EDITOR',
  CONTROLS_PANEL = 'CONTROLS_PANEL',
  HEADER = 'HEADER',
  CONTROL_EDITOR_PANEL = 'CONTROL_EDITOR_PANEL',
  ACCORDION = 'ACCORDION',
  BLOCKS_PANEL = 'BLOCKS_PANEL',
  BLOCK_THUMB = 'BLOCK_THUMB',
  PREVIEW_CONTAINER = 'PREVIEW_CONTAINER ',
  HISTORY = 'HISTORY'
}

export enum ControlType {
  BASE = 'BASE'
}

export enum InputType {
  TEXT = 'TEXT',
  COLOR = 'COLOR',
  NUMBER = 'NUMBER',
  SELECTPICKER = 'SELECTPICKER',
  TOGGLER = 'TOGGLER',
  BUTTON = 'BUTTON',
  DYNAMIC = 'DYNAMIC',
  LABEL = 'LABEL',
  INPUT_EDIT = 'INPUT_EDIT'
}

export enum ControlUpdateType {
  DELETE = 'DELETE',
  INSERT = 'INSERT',
  MODIFY = 'MODIFY',
  NONE = 'NONE',
}

export type UIObjectTypes = UIObjectType | ControlType | InputType;


export interface ControlValueAccessor {
  transformer: boolean;
  structural: boolean;

  getModifiedPath?(): Path;

  getModifiedValue?(): any;

  getRawValue(): any;

  getValue(valuePath: string | number): any;

  setValue(valuePath: string | number, value: any): any;

  isValid(): boolean;
}


export interface IterableControlValueAccessor extends ControlValueAccessor, Iterable<any> {

  updateItem(valuePath: string | number, value: any): any;

  getItem(valuePath: string | number): any;

  removeItem(valuePath: string | number): any;

  addItem(item: any): any;
}

export enum ComparisonType {
  EQUAL = '===',
  NOT_EQUAL = '!==',
  CAST_EQUAL = '=='
}

export enum ConditionConnection {
  OR = 'OR',
  AND = 'AND'
}

export interface DisplayCondition {
  prop1: string;
  prop2?: string;
  value?: any;
  comparison: ComparisonType;
  connection?: ConditionConnection;
}

export interface UIObject extends HtmlObject {
  id?: string;
  tag: UIObjectTypes;
  children: UIObject[];
  localState?: any;
  if?: DisplayCondition[];
  iterate?: string;
  bindings?: UINodeBinding;
}

export interface IterableValueItem {
  key: string | number;
  value: any;
}

export interface UINodeBinding {
  [key: string]: string;
}

export interface SettingsObject extends UIObject {
  value?: any;
  /**
   * операція яка буде виконана на батьківському value accessor
   */
  updateType?: ControlUpdateType;
  id?: string;
}

export interface ControlObject extends SettingsObject {
  tag: ControlType;
  children: (ControlObject | InputObject)[];
  // object that will provide control's children requested values
  valueAccessor: ControlValueAccessorType;
}

export interface InputObject extends SettingsObject {
  tag: InputType;
  dynamicFromTypes?: InputType[];
}


export interface BlockConfig {
  controls: ControlObject[];
}

export enum ControlValueAccessorType {
  STYLE = 'STYLE',
  TEXT = 'TEXT',
  FORM = 'FORM',
  DOM = 'DOM',
  GROUP_DOM = 'GROUP_DOM',
  BUFF = 'BUFF'
}

export enum UINodeEventType {
  CONTROL_VALUE_UPDATE = 'CONTROL_VALUE_UPDATE',
  CONTROL_PROPERTY_UPDATED = 'CONTROL_PROPERTY_UPDATED',
}

export interface UINodeEvent {
  type: UINodeEventType;
  emitter?: any;
}

export interface UpdateSummary {
  modifiedPath: Path;
  modifiedProperty: string;
  value: any;
  // updateType: ControlUpdateType;
  dataMethod: DataMethod;
}

export interface ControlUpdatedEvent extends UINodeEvent {
  emitter?: SettingsObject;
  // modifiedPath?: Path;
  summary?: UpdateSummary;
}

/**
 * Режим відображення
 */
export enum UIRendererMode {
  EDIT = 'EDIT',
  DISPLAY = 'DISPLAY'
}

