import {
  Component,
  ComponentFactoryResolver,
  Injector, Input,
  OnDestroy,
  OnInit,
  Type,
  ViewContainerRef
} from '@angular/core';
import {BaseDomRenderer} from '../base.dom-renderer';
import {DocumentNodeComponent} from '../../../document/document-node-component/document-node.component';
import {BaseNodeComponent} from '../base.node-component';
import {documentNodeComponents, documentNodeComponentsMap} from '../../../document/document-node-component/document-node-components';
import {HtmlObject} from '../../../../../../../ui-editor-common/entities/HtmlObject';
import {BaseNodes} from '../../../../entities/BaseNodes';

/**
 * Відповідає за відображення документу (email)
 * <br/>Відображає node типу {@link HtmlObject}
 */
@Component({
  selector: 'document-renderer',
  template: '',
  entryComponents: documentNodeComponents,
})
export class DocumentRendererComponent extends BaseDomRenderer implements OnInit, OnDestroy {
  @Input() node: HtmlObject;
  component: DocumentNodeComponent;

  constructor(injector: Injector, componentFactoryResolver: ComponentFactoryResolver, viewContainerRef: ViewContainerRef) {
    super(injector, componentFactoryResolver, viewContainerRef);
  }

  protected getComponentType(): Type<BaseNodeComponent> {
    if (this.node.externalBlock) {
      return documentNodeComponentsMap[BaseNodes.CUSTOM_BLOCK_WRAPPER];
    }
    return documentNodeComponentsMap[this.node.tag] || documentNodeComponentsMap['DIV'];
  }
}
