import {HtmlObject} from '../../../common/entities/HtmlObject';
import {Injectable} from '@angular/core';
import {HtmlDocument} from '../../../common/entities/HtmlDocument';
import {NodeUtils} from '../../../common/tools/NodeUtils';
import {DeleteCommand, InsertCommand, MoveCommand, UpdateCommand} from '../../../../ui-editor-common/entities/Command';
import {EditorState} from './state.types';
import {DocumentSetEvent, EditorEventType, PatchEvent} from '../entities/EditorEvent';
import {Reducer} from '../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {EventsService} from './events.service';
import {AsyncValue} from '../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../tools/decorators/StateStream/StateStream.decorator';
import {Patch} from '../../../common/entities/Patch';
import {LogMethod} from '../tools/decorators/Log/Log.decorator';

/** @todo unify with dom data service, make non instance methods static*/
@Injectable()
@EditorEventListener()
export class DocumentStateService {

  // todo probably separate css state should be created
  @AsyncValue()
  @StateStream({stateProperty: 'document'})
  // @ts-ignore
  private document: HtmlDocument/* = this.document*/;


  public constructor() {
    console.log('document state:', this);
  }

  @Reducer({type: EditorEventType.SET_DOCUMENT, stateProperty: 'document'})
  private onDocumentSet(state: EditorState, e: DocumentSetEvent): HtmlDocument {
    return e.data;
  }

  @Reducer({type: EditorEventType.EXECUTE_PATCH, stateProperty: 'document'})
  private onPatch(state: HtmlDocument, e: PatchEvent): HtmlDocument {
    // @ts-ignore
    e.data.actions.forEach(action => this[action.do.method](action.do, state));
    return state;
  }

  private updateNode(command: UpdateCommand, data: HtmlDocument): HtmlObject {
    if (command.path && command.path.length) {
      return NodeUtils.updateNode(data.html, command.path, command.attribute, command.value);
    }
    return command.value;
  }

  private moveNodes(command: MoveCommand, data: HtmlDocument): HtmlObject[] {
    const removedNodes = NodeUtils.removeNodes(data.html, command.path, 1);
    return NodeUtils.insertNodes(data.html, command.destinationPath, removedNodes);
  }

  private insertNodes(command: InsertCommand, data: HtmlDocument): HtmlObject[] {
    return NodeUtils.insertNodes(data.html, command.path, command.nodes);
  }

  private removeNodes(command: DeleteCommand, data: HtmlDocument): HtmlObject[] {
    return NodeUtils.removeNodes(data.html, command.path, command.count);
  }

  // @LogMethod()
  public getChildByPath(path: number[], getClosest = true): HtmlObject {
    return NodeUtils.getChildByPath(this.document.html, path, getClosest);
  }

  public getDocumentId(): string {
    return this.document.id;
  }

  public setDocument(document: HtmlDocument): void {
    EventsService.dispatch({type: EditorEventType.SET_DOCUMENT, data: document});
  }

  public executePatch(patch: Patch): void {
    EventsService.dispatch({type: EditorEventType.EXECUTE_PATCH, data: patch});
  }
}
