import {BaseControlComponent} from '../base/base-control.component';
import {ChangeDetectionStrategy, Component, HostBinding, OnInit} from '@angular/core';
import {
    ControlObject,
    ControlType,
    ControlUpdateType,
    ControlValueAccessorType,
    InputType,
    UINodeEventType,
    UIRendererMode
} from '../../../../../entities/UIObject';
import {ControlUtils} from '../../../../../tools/ControlUtils';
import {NodeUtils} from '../../../../../../../../ui-editor-common/tools/NodeUtils';
import {SelectItem} from '../../inputs/select/select.component';
import {BaseControlEditSettings} from '../../BaseControlEditSettings';
import {filter} from 'rxjs/operators';
import {MixinsDecorator} from '../../../../../tools/decorators/Mixins/Mixins.decorator';
import {EditorEvent} from '../../../../../entities/EditorEvent';
import {UpdateCommand} from '../../../../../../../../ui-editor-common/entities/Command';

/**
 * Базовий компонет редагування контрола
 */
@Component({
    selector: 'base-control-edit',
    templateUrl: './base-control-edit.component.html',
    styleUrls: ['./base-control-edit.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
@MixinsDecorator([BaseControlEditSettings])
export class BaseControlEditComponent extends BaseControlComponent implements /*DoCheck,*/ OnInit {
    @HostBinding('attr.draggable') draggable = true;
    rendererMode = UIRendererMode.EDIT;
    valueAccessors: SelectItem[];
    updateTypes: SelectItem[];
    collapsed = false;
    showAdditionalSettings = false;

    ngOnInit(): void {
        super.ngOnInit();
        this.valueAccessors = Object.entries(ControlValueAccessorType).map(([key, value]) => ({value, label: key}));
        this.updateTypes = Object.entries(ControlUpdateType).map(([key, value]) => ({value, label: key}));
    }

    private saveControl() {
        NodeUtils.reIndexNodeChildren(this.node);
        this.applicationState.controlsDataService
            .updateNode({
                path: this.node.path,
                value: this.node
            } as UpdateCommand);
        this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
    }

    public addControl(): void {
        this.node.children.push({
            ...ControlUtils.defaultControlProperties,
            tag: ControlType.BASE,
            valueAccessor: ControlValueAccessorType.BUFF,
            children: [],
            bindings: {value: ControlUtils.valueAccessorProp},
        });
        this.saveControl();
    }

    public addInput(): void {
        this.node.children.push({
            ...ControlUtils.defaultControlProperties,
            tag: InputType.TEXT,
            children: [],
            bindings: {value: ControlUtils.valueAccessorProp}
        });
        this.saveControl();
    }

    public renameValueAccessor(valueAccessor: ControlValueAccessorType): void {
        this.node.valueAccessor = valueAccessor;
        if (valueAccessor === ControlValueAccessorType.STYLE) {
            this.node.bindings = {value: `${this.node.path.length ? ControlUtils.valueAccessorProp : 'editedNode'}.attributes.style`};
        }
        this.setValueAccessor();
        this.saveControl();
    }

    // protected isOverCurrentNode() {
    //   return filter((e: EditorEvent) => {
    //       const result = !this.dragged &&
    //         !!this.node.path.length &&
    //         (e.data.target === this.el.nativeElement ||
    //           (<Element>e.data.target).closest('base-control-edit') === this.el.nativeElement) &&
    //         !(<Element>e.data.target).closest('base-input-edit');
    //       return result;
    //     }
    //   );
    // }

    protected isOverCurrentNode(e: EditorEvent): boolean {
        return !this.dragged &&
            !!this.node.path.length &&
            (e.data.target === this.el.nativeElement ||
                (<Element>e.data.target).closest('base-control-edit') === this.el.nativeElement) &&
            !(<Element>e.data.target).closest('base-input-edit');
    }

    protected onDragEnter(e: EditorEvent) {
        super.onDragEnter(e);
        this.cdr.markForCheck();
    }

    // todo ACTUALIZE
    protected moveInto(e): void {
        // this.globalState.controlsDataService.removeNodes([this.globalState.draggedControl]);
        // this.globalState.controlsDataService.insertNodes([this.getNodeForInsert(this.globalState.draggedControl, e, true)]);
        // NodeUtils.reIndexNodeChildren(this.node);
        // this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
        this.onDragStop();
    }

    protected copyInto(e: Event): void {
        // this.globalState.controlsDataService.insertNodes([cloneDeep(this.getNodeForInsert(this.globalState.draggedControl, e))]);
        // NodeUtils.reIndexNodeChildren(this.node);
        // this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
        this.onDragStop();
    }


    public setIteration(iterate: boolean): void {
        if (iterate) {
            this.node.iterate = ControlUtils.valueAccessorProp;
            this.node.bindings = {value: 'value'};
        } else {
            this.node.iterate = undefined;
            this.node.bindings = {value: ControlUtils.valueAccessorProp};
        }
        this.saveControl();
    }

    public isIterable(): boolean {
        if (!this.node.path.length) {
            return false;
        }
        const parent = NodeUtils.getChildByPath(
            this.applicationState.controlsDataService.data.html, this.node.path.slice(0, -1)
        ) as ControlObject;
        return [ControlValueAccessorType.DOM, ControlValueAccessorType.STYLE].indexOf(parent.valueAccessor) >= 0;
    }
}
