import {EditorEventType} from '../../entities/EditorEvent';
import {EditorReducer} from '../../state/state.types';
import {InjectionToken} from '@angular/core';

export interface SubscriberOptions {
    key?: string;
    className?: string;
    context?: any;
    previousSentValue?: any;
}

export interface EventSubscriberOptions extends SubscriberOptions {
    type: EditorEventType;
    pipes?: string;
    condition?: string;
}

export interface ReducerOptions extends SubscriberOptions {
    type: EditorEventType;
    reducer?: EditorReducer;
    stateProperty?: string;
}

export interface StateStreamOptions {
    stateProperty?: string;
}

export interface StateSubscriberOptions extends StateStreamOptions, SubscriberOptions {
    definedOnly?: boolean;
}

export interface InjectedValueOptions {
    token: string | InjectionToken<any> | any,
    dynamicInjector?: boolean;
}

export interface EmitterOptions {
    type: EditorEventType;
    unsubscribeImmediately?: boolean;
}

export enum SubscriberType {
    subscriber = '_subscribers',
    reducer = '_reducers',
    stateSubscriber = '_stateSubscribers',
}

export const destroyEmitter = '_componentDestroyed$';


