import {Observable, Subscription} from 'rxjs';

export interface SharedSubscriptionSubscriber<T> {
    invoke: Function;
    options: T;
}

export type SharedSubscriptionChecker = (subscriber: any, event: any) => boolean;

export class SharedSubscription<T> {
    private subscribers: SharedSubscriptionSubscriber<T>[] = [];
    private subscription: Subscription;
    private lastEvent: any;

    constructor(private observable: Observable<any>,
                private conditionChecker: SharedSubscriptionChecker = () => true,
                private removeChecker: SharedSubscriptionChecker = () => false,
                private replay = false) {
        this.subscription = this.observable.subscribe(this.onEvent.bind(this));
    }

    private handleSubscriber(s: SharedSubscriptionSubscriber<T>, e): void {
        if (this.removeChecker(s.options, e)) {
            this.removeSubscriber(s);
            return;
        }
        if (this.conditionChecker(s.options, e)) {
            s.invoke.call(s.options, e);
        }
    }

    private onEvent(e: any): void {
        this.replay && (this.lastEvent = e);
        for (let i = this.subscribers.length - 1; i >= 0; i--) {
            // if (this.removeChecker(this.subscribers[i].options, e)) {
            //     this.removeSubscriber(this.subscribers[i]);
            //     continue;
            // }
            // if (this.conditionChecker(this.subscribers[i].options, e)) {
            //     this.subscribers[i].invoke.call(this.subscribers[i].options, e);
            // }
            this.handleSubscriber(this.subscribers[i], e);
        }
    }

    public removeSubscriber(subscriber: SharedSubscriptionSubscriber<T>): void {
        this.subscribers = this.subscribers.filter(s => s !== subscriber);
    }

    public addSubscriber(s: SharedSubscriptionSubscriber<T>): Function {
        this.subscribers.push(s);
        this.lastEvent && this.handleSubscriber(s, this.lastEvent);
        return () => this.removeSubscriber(s);
    }

    public destroy(): void {
        this.subscription.unsubscribe();
        delete this.subscribers;
    }
}