import {ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Injector, OnInit} from '@angular/core';
import {EventsService} from '../../../state/events.service';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {BehaviorableNode} from '../../../entities/BehaviorableNode';
import {
  ControlObject,
  ControlType,
  ControlUpdatedEvent,
  ControlValueAccessorType,
  UINodeEventType
} from '../../../entities/UIObject';
import {ActionsService} from '../../../domain/actions.service';
import {ControlUtils} from '../../../tools/ControlUtils';
import {HtmlObject} from '../../../../../../ui-editor-common/entities/HtmlObject';
import {BaseNodesContainerComponent} from '../base-nodes-container/base-nodes-container.component';
import {filter} from 'rxjs/operators';
import {detailedDiff} from 'deep-object-diff';
import {EditorEvent, EditorEventType} from '../../../entities/EditorEvent';
import {StateSubscriber} from '../../../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {LogClass} from '../../../tools/decorators/Log/Log.decorator';

/**
 * Компонент для відображення панелі контролів, потомок {@link BaseNodesContainerComponent}
 * мапиться на відображення по типу CONTROLS_PANEL {@link UIObjectType}
 * при зміні нода в applicationState.selectedNode додає кнопку "Add control"
 */
@Component({
  selector: 'control-panel',
  templateUrl: '../ui-node-component/ui-node.component.html',
  styleUrls: ['./control-panel.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
@EditorEventListener()
// @LogClass()
export class ControlPanelComponent extends BaseNodesContainerComponent implements OnInit {
  editedNode: HtmlObject;

  public constructor(events: EventsService,
                     public actionManager: ActionManagerService,
                     public actions: ActionsService,
                     cdr: ChangeDetectorRef,
                     el: ElementRef,
                     injector: Injector) {
    super(cdr, el, events, injector);
  }

  /**
   * Додає кнопку "Add control" і усі контроли обранного ноду коли селектиться Node в node children
   * @param node
   */
  @StateSubscriber({stateProperty: 'applicationState.selectedNode', definedOnly: true})
  protected onNodeSelected(node: BehaviorableNode) {
    this.editedNode = node;
    this.node.children = [
      {
        ...ControlUtils.defaultControlProperties,
        tag: ControlType.BASE,
        valueAccessor: ControlValueAccessorType.BUFF,
        children: [
          ControlUtils.getButton({bindTo: 'applicationState.addingControl', label: 'Add control', value: true})
        ]
      } as ControlObject,
      ...node.config.controls
    ];
    // this.cdr.detectChanges();
    this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
  }

  /**
   * Запускає екшени {@link ActionManagerService#runActions} для типу [UINodeEventType.CONTROL_VALUE_UPDATE]{@link UINodeEventType#CONTROL_VALUE_UPDATE}
   * @param {ControlUpdatedEvent} event
   */
  public onComponentEvent(event: ControlUpdatedEvent): void {
    console.log('%c\nonComponentEvent:', 'font-weight:bold;', event);
    if (event.type === UINodeEventType.CONTROL_VALUE_UPDATE) {

      console.log('update summary:', event.summary);
      // event.emitter.value = cloneDeep(event.emitter.value);
      // const nodeBefore = cloneDeep(this.editedNode);
      // set(this, event.emitter.bindings.value, event.emitter.value);
      // console.log('updated node:', this.editedNode);
      // console.log('detailedDiff', detailedDiff(nodeBefore, this.editedNode));

      // this.actionManager.runActions([
      //   this.actions.getNodeUpdateAction(this.editedNode)
      // ]);

      this.actionManager.runActions([
        this.actions.getActionBySummary(this.editedNode, event.summary)
      ]);
    }
  }
}
