import {ChangeDetectorRef, Component, ElementRef, HostListener, Injector} from '@angular/core';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {UINodeComponent} from '../ui-node-component/ui-node.component';
import {BehaviorableNode} from '../../../entities/BehaviorableNode';
import {EventsService} from '../../../state/events.service';
import {EditorEventType} from '../../../entities/EditorEvent';

/**
 * Компонент відображення хідеру UI, потомок {@link UINodeComponent}
 * <br/> оброблює кліки undo, redo, showHistory за допомогою {@link ActionManagerService}
 */

@Component({
  selector: 'header-component',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.less']
})
export class HeaderComponent extends UINodeComponent {

  editedNode: BehaviorableNode;

  public constructor(public actionManager: ActionManagerService,
                     el: ElementRef,
                     cdr: ChangeDetectorRef,
                     events: EventsService,
                     injector: Injector) {
    super(cdr, el, events, injector);
  }

  @HostListener('click', ['$event'])
  onHeaderClick() {
    this.applicationState = {selectedNode: undefined};
  }

  public undo(e: Event): void {
    e.stopPropagation();
    this.actionManager.undo();
  }

  public redo(e: Event): void {
    e.stopPropagation();
    this.actionManager.redo();
  }

  public showHistory(e: Event): void {
    this.applicationState = {
      addingControl: false,
      editedControl: undefined,
      // editedNode: undefined, // todo where did it come from???
      viewingHistory: !this.applicationState.viewingHistory,
    };
  }
}
