import {Injectable, InjectionToken, Injector} from '@angular/core';
import {ReplaySubject} from 'rxjs';

@Injectable()
export class ExtraModuleInjector {
  private static injector: Injector;
  public static injectorValue: ReplaySubject<any> = new ReplaySubject(1);

  public static get(token: string | InjectionToken<any> | any) {
    if (ExtraModuleInjector.injector) {
      return ExtraModuleInjector.injector.get(token);
    }
  }

  constructor(public injector: Injector) {
    ExtraModuleInjector.injector = injector;
    ExtraModuleInjector.injectorValue.next(ExtraModuleInjector.injector);
  }
}
