import {
    AfterViewChecked,
    ChangeDetectionStrategy,
    Component,
    DoCheck,
    HostBinding,
    OnInit,
    ViewChild,
} from '@angular/core';
import {
    ControlUpdateType,
    InputObject,
    InputType,
    UINodeEvent,
    UINodeEventType,
} from '../../../../../entities/UIObject';
import {BaseInputComponent} from '../base.input.component';
import {BaseControlEditSettings} from '../../BaseControlEditSettings';
import {isEqual} from 'lodash';
import {filter} from 'rxjs/operators';
import {SelectItem} from '../select/select.component';
import {MixinsDecorator} from '../../../../../tools/decorators/Mixins/Mixins.decorator';
import {EditorEvent} from '../../../../../entities/EditorEvent';
import {UpdateCommand} from '../../../../../../../../ui-editor-common/entities/Command';

/**
 * Базовий компонент додавання/редагування інпуту
 */
@Component({
    selector: 'base-input-edit',
    templateUrl: './base-input-edit.component.html',
    styleUrls: ['./base-input-edit.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
@MixinsDecorator([BaseControlEditSettings])
export class BaseInputEditComponent extends BaseInputComponent implements DoCheck, OnInit, AfterViewChecked {

    @HostBinding('attr.draggable') draggable = true;
    @ViewChild('renderer') renderer;

    showAdditionalSettings = false;
    inputTypes: SelectItem[] = Object.entries(InputType).map(([key, value]) => ({value, label: key}));
    updateTypes: SelectItem[] = Object.entries(ControlUpdateType).map(([key, value]) => ({value, label: key}));
    tag: string;

    ngOnInit() {
        super.ngOnInit();
        this.tag = this.node.tag;
    }

    ngDoCheck(): void {
        if (!isEqual(this.node, this.container.node)) {
            this.node = <InputObject>this.container.node;
            this.node.localState && Object.assign(this.localState, this.node.localState);
        }
        if (this.node && !isEqual(this.node.localState, this.localState)) {
            this.updateLocalState();
        }

    }

    ngAfterViewChecked(): void {
        if (this.node.tag !== this.tag) {
            this.tag = this.node.tag;
            this.renderer.updateComponent();
        }
    }

    private saveControl() {
        this.applicationState.controlsDataService.updateNode({
            path: this.node.path,
            value: this.node
        } as UpdateCommand);

        this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
    }

    public onComponentEvent(event: UINodeEvent, iterableComponentIndex?: string | number): void {
        if (this.node) {
            this.uiNodeEvent.emit(event);
        }
    }

    // protected isOverCurrentNode() {
    //   return filter((e: EditorEvent) => !this.dragged &&
    //     this.node.path.length &&
    //     (e.data.target === this.el.nativeElement ||
    //       (<Element>e.data.target).closest('base-input-edit') === this.el.nativeElement));
    // }

    protected isOverCurrentNode(e: EditorEvent): boolean {
        return !this.dragged &&
            this.node.path.length &&
            (e.data.target === this.el.nativeElement ||
                (<Element>e.data.target).closest('base-input-edit') === this.el.nativeElement);
    }

    protected onDragEnter(e: EditorEvent) {
    }

    protected onDrop(e: EditorEvent): void {
    }

    public changeInputType(type: InputType): void {
        this.node.tag = type;
        this.saveControl();
    }

    public setLocalStateProperty(property: string, value: string) {
        this.node.localState[property] = value;
        this.saveControl();
    }

}
