import {Injectable} from '@angular/core';
import {EventsService} from '../state/events.service';
import {HttpClient} from '@angular/common/http';
import {EditorEvent, EditorEventType} from '../entities/EditorEvent';
import {WebsocketService} from '../api/websocket.service';
import {Patch} from '../../../../ui-editor-common/entities/Patch';
import {Action} from '../../../../ui-editor-common/entities/Action';
import {NodeWrapperService} from '../services/node-wrapper/node-wrapper.service';
import {HtmlNodeType} from '../../../../ui-editor-common/entities/HtmlObject';
import {cloneDeep} from 'lodash';
import {OperationUtils} from '../tools/OperationUtils';
import {EventSubscriber} from '../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {ApplicationState} from '../state/state.types';
import {StateStream} from '../tools/decorators/StateStream/StateStream.decorator';
import {AsyncValue} from '../tools/decorators/AsyncValue/AsyncValue.decorator';
import {DocumentStateService} from '../state/document.state.service';

@Injectable()
@EditorEventListener()
export class ActionManagerService {
  @AsyncValue()
  @StateStream({stateProperty: 'applicationState'})
  // @ts-ignore
  private applicationState: ApplicationState/* = this.applicationState*/;

  public undoStack: Patch[] = [];
  public redoStack: string[] = [];
  // todo POC
  public documentVersion: string = 'initial';


  public constructor(private events: EventsService,
                     private http: HttpClient,
                     private ws: WebsocketService,
                     private nodeWrapper: NodeWrapperService,
                     private documentState: DocumentStateService) {
    window['actions'] = this;
  }

  private getPatch(actions: Action[], replay = false): Patch {
    console.log('%ccreating patch object:', 'color:red;', this.undoStack);
    const patch = {
      actions,
      documentId: this.documentState.getDocumentId(),
      time: new Date().getTime(),
      // todo POC
      id: `patch${replay ? `_replay` : ``}_${new Date().getTime()}_${Math.random()}`,
      previousPatchId: this.undoStack.length ? this.undoStack[this.undoStack.length - 1].id : undefined,
      replay,
      confirmed: false,
      documentVersion: this.documentVersion
    };
    console.log('CREATED PATCH:', patch);
    return patch;
  }

  // todo POC
  private prepareRemotePatch(patch: Patch): Patch {
    console.log('\nprepareRemotePatch', patch);
    patch = {
      ...patch,
      remote: true,
      replay: true
    };

    console.log('this.documentVersion', this.documentVersion);
    console.log('patch.documentVersion', patch.documentVersion);
    if (this.documentVersion !== patch.documentVersion) {
      console.log('UPDATE');
      const commonHistoryPointIndex = this.undoStack.findIndex(p => p.documentVersion === patch.documentVersion);
      if (commonHistoryPointIndex >= 0) {
        const preparedActions = OperationUtils
          .transformActions(
            patch.actions,
            this.undoStack.slice(commonHistoryPointIndex, this.undoStack.length)
              // .filter(p => !p.remote)
              .filter(p => !p.remote || (p.remote && p.documentVersion !== patch.documentVersion))
              .map(p => p.actions)
          );
        console.log('prepared actions:', preparedActions);
        patch.actions = preparedActions;
        // patch.documentVersion = this.documentVersion;
      }
    }

    console.log('prepared patch', patch);
    return patch;

  }

  @EventSubscriber({type: EditorEventType.REMOTE_UPDATE})
  private executeRemotePatch(e: EditorEvent): void {
    let patch = e.data;
    console.log('%cexecuteRemotePatch', 'color:orange;font-weight:bold;', patch);
    // todo POC
    patch = this.prepareRemotePatch(patch);

    this.executePatch(patch);
    this.undoStack.push(patch);
    // this.documentVersion = patch.id;
  }

  private executePatch(patch: Patch): void {
    console.log('%cexecuting actions:', 'font-weight:bold;', cloneDeep(patch.actions));

    // @ts-ignore
    this.documentState.executePatch(patch);

    // this.documentVersion = patch.id;
    if (this.applicationState.selectedNode) {
      let nodeToReselect = this.documentState.getChildByPath(this.applicationState.selectedNode.path);
      if (nodeToReselect.type === HtmlNodeType.TEXT_NODE) {
        nodeToReselect = this.documentState.getChildByPath(this.applicationState.selectedNode.path.slice(0, -1));
      }
      this.nodeWrapper.selectNode(nodeToReselect);
    }
  }

  private executeUserPatch(patch: Patch): void {
    this.executePatch(patch);
    // this.ws.savePatch(patch);
  }

  public undo(): void {
    console.log('\n');
    const patchToUndo = [...this.undoStack].reverse()
      .find(p =>
        !p.remote && !p.replay && !this.redoStack.find(patchId => patchId === p.id));

    console.log('patchToUndo', patchToUndo);

    if (patchToUndo) {
      const actionsToDo = OperationUtils
        .reverseActions(
          OperationUtils
            .transformActions(
              patchToUndo.actions,
              this.getPatchesSince(patchToUndo, true, true)
                .map(p => p.actions)
            )
        );
      console.log('actionsToDo', actionsToDo);

      this.redoStack.unshift(patchToUndo.id);
      this.runActions(actionsToDo, true);
    }
  }

  public redo(): void {
    console.log('\n');
    const id = this.redoStack.shift();
    console.log('id to redo', id);
    if (id) {
      const patchToRedo = this.undoStack.find(p => p.id === id);
      console.log('patchToRedo', patchToRedo);
      const actionsToRedo = OperationUtils
        .transformActions(
          patchToRedo.actions,
          [
            patchToRedo.actions,
            ...this.getPatchesSince(patchToRedo, true, true)
              .map(p => p.actions)
          ]
        );
      console.log('actions to redo:', actionsToRedo);
      this.runActions(actionsToRedo, true);
    }
  }

  public runActions(actions: Action[], replay = false): void {
    const patch = this.getPatch(actions, replay);
    this.executeUserPatch(patch);
    this.undoStack.push(cloneDeep(patch));

    // todo POC
    this.ws.connected && this.undoStack
      .filter(p => !p.sent)
      .forEach(p => {
        p.sent = true;
        this.ws.savePatch(p);

        this.documentVersion = p.id;
        // p.documentVersion = this.documentVersion;
      });


    if (!replay) {
      this.redoStack = [];
    }
  }


  private getPatchesSince(patch: Patch, includeReplays = true, includeOwn = true): Patch[] {
    console.log('getPatchesSince', patch, includeReplays, includeOwn);
    const patchIndex = this.undoStack.findIndex(p => p.id === patch.id);
    console.log('patchIndex', patchIndex);
    if (patchIndex < 0) {
      return [];
    }
    return this.undoStack
      .slice(patchIndex + 1, this.undoStack.length)
      .filter(p => includeReplays ? true : !p.replay)
      .filter(p => includeOwn ? true : p.replay);

  }

  public cancelSinglePatch(patch: Patch): void {
    console.log('cancelSinglePatch', patch);
    const cancelActions = OperationUtils
      .transformActions(patch.actions, this.getPatchesSince(patch).map(p => p.actions))
      .map(action => ({do: action.undo, undo: action.do}));
    console.log('transformed actions:', cancelActions);
    this.runActions(cancelActions, true);
  }
}
