/** @description merges given constructors into decorated one, adds each property except constructor */
export function MixinsDecorator(constructors: Function[]) {
  return function (constructor: Function) {
    constructors
      .forEach(c => {
        Object
          .getOwnPropertyNames(c.prototype)
          .filter(name => name !== 'constructor')
          .forEach(name => {
            Object.defineProperty(constructor.prototype, name, Object.getOwnPropertyDescriptor(c.prototype, name));
          });
      });
  };
}
