import {ChangeDetectorRef, Component, ElementRef, Injector} from '@angular/core';
import {UINodeComponent} from '../ui-node-component/ui-node.component';
import {EventsService} from '../../../state/events.service';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {Patch} from '../../../../../../ui-editor-common/entities/Patch';

/**
 * Компонент для відображення історії змін, потомок {@link UINodeComponent}
 */
@Component({
  selector: 'history-component',
  templateUrl: './history.component.html',
  styleUrls: ['./history.component.less']
})
export class HistoryComponent extends UINodeComponent {

  public constructor(public actions: ActionManagerService,
                     el: ElementRef,
                     cdr: ChangeDetectorRef,
                     events: EventsService,
                     injector: Injector) {
    super(cdr, el, events, injector);
  }

  public cancel(patch: Patch): void {
    this.actions.cancelSinglePatch(patch);
  }
}
