import {EventsService} from './events.service';
import {EditorState} from './state.types';
import {ConnectableObservable, Observable} from 'rxjs';
import {distinctUntilChanged, map, publishReplay, scan} from 'rxjs/operators';
import {EditorEvent} from '../entities/EditorEvent';
import {Injectable} from '@angular/core';
import {EventSubscriberOptions, ReducerOptions, StateSubscriberOptions} from '../tools/decorators/decorator.types';
import {get, set} from 'lodash';
import {LogMethod} from '../tools/decorators/Log/Log.decorator';
import {SharedSubscription} from '../tools/SharedSubscription';

@Injectable()
export class StateManagerService {
    private static reducersMap: { [type: string]: ReducerOptions[] } = {};
    private static initialState: EditorState = {} as any;
    private static reducers: ReducerOptions[] = [];
    private static $state: ConnectableObservable<EditorState> = EventsService.getDispatcher()
        .pipe(
            scan(StateManagerService.reducer, StateManagerService.initialState),
            distinctUntilChanged(),
            publishReplay(1),
        ) as ConnectableObservable<EditorState>;
    private static subscribers = new SharedSubscription<StateSubscriberOptions>(
        StateManagerService.$state,
        StateManagerService.isTargetSubscriber,
        StateManagerService.isInvalidSubscriber,
        true
    );

    constructor() {
        StateManagerService.$state.connect();
        console.log('CONNECTED', StateManagerService.$state);
        console.log('state:', this);
        StateManagerService.$state.subscribe(value => {
            console.log('%cSTATE UPDATED:', 'font-weight:bold;color:red;', value);
        });
    }

    private static isTargetSubscriber(subscriber: StateSubscriberOptions, state: EditorState): boolean {
        // console.log("isTargetSubscriber", subscriber, state)
        // if (!subscriber.stateProperty) {
        //     return true;
        // }
        // return get(state, subscriber.stateProperty) !== get(StateManagerService.previousState || {}, subscriber.stateProperty);

        const value = !subscriber.stateProperty ? state : get(state, subscriber.stateProperty);
        // const previousValue = !subscriber.stateProperty ? StateManagerService.previousState :
        //     get(StateManagerService.previousState, subscriber.stateProperty);
        const previousValue = subscriber.previousSentValue;

        const r =  value !== previousValue && (!subscriber.definedOnly || !!value);
        // console.log("check:",r);
        return  r;
    }

    private static isInvalidSubscriber(subscriber: StateSubscriberOptions): boolean {
        return subscriber.context.destroyed;
    }

    private static reducer(state: EditorState, action: EditorEvent): EditorState {
        const matchingReducers = StateManagerService.reducersMap[action.type] || [];
        matchingReducers.forEach(r => {
            try {
                state = StateManagerService.applyReducer(state, action, r);
            } catch (e) {
                console.error('Reducer error:', r, e);
            }
        });
        return state;
    }

    // @LogMethod()
    private static applyReducer(state: EditorState, action: EditorEvent, options: ReducerOptions): EditorState {
        if (!options.stateProperty) {
            return {...options.reducer(state, action)};
        }
        const oldPropertyValue = get(state, options.stateProperty);
        const newPropertyValue = {...options.reducer(oldPropertyValue, action)};
        state = {...state};
        set(state, options.stateProperty, newPropertyValue);
        return state;
    }

    public static addReducer(options: ReducerOptions): Function {
        StateManagerService.reducersMap[options.type] = StateManagerService.reducersMap[options.type] || [];
        StateManagerService.reducersMap[options.type].push(options);
        return () => {
            StateManagerService.reducersMap[options.type] = StateManagerService.reducersMap[options.type].filter(o => o !== options);
        };
    }

    public static getStatePropertyStream(prop: string): Observable<EditorState | any> {
        return StateManagerService.$state.pipe(map(state => prop ? get(state, prop) : state));
    }

    public static addSubscriber(subscriber: StateSubscriberOptions): Function {
        return this.subscribers.addSubscriber({
            options: subscriber,
            invoke: function (state) {
                const value = subscriber.stateProperty ? get(state, subscriber.stateProperty) : state;
                this.previousSentValue = value;
                subscriber.context[subscriber.key].call(subscriber.context, value);
            }
        });
    }
}
