import {Injectable} from '@angular/core';
import {EditorEventListener} from '../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {AsyncValue} from '../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../tools/decorators/StateStream/StateStream.decorator';
import {UIObject} from '../entities/UIObject';
import {EventsService} from './events.service';
import {Reducer} from '../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventType, UISetEvent} from '../entities/EditorEvent';
import {EditorState} from './state.types';

@Injectable()
@EditorEventListener()
export class UIState {
  @AsyncValue()
  @StateStream({stateProperty: 'ui'})
  // @ts-ignore
  public ui: UIObject/* = this.ui*/;

  public constructor(private events: EventsService) {
    console.log('UI state:', this);
  }

  @Reducer({type: EditorEventType.SET_UI, stateProperty: 'ui'})
  private onUISet(state: EditorState, e: UISetEvent): UIObject {
    return e.data;
  }

  public setUI(ui: UIObject): void {
    EventsService.dispatch({type: EditorEventType.SET_UI, data: ui});
  }
}
