import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {HtmlDocument} from '../../../../ui-editor-common/entities/HtmlDocument';
import {Control} from '../../../../ui-editor-common/entities/Control';
import {CustomBlock} from '../../../../ui-editor-common/entities/CustomBlock';
import {ConfigStateService} from '../state/config.state.service';

// todo needs complete refactoring
@Injectable()
export class RepositoryService {

  public constructor(private http: HttpClient,
                     private configState: ConfigStateService) {
  }

  private getUrl(url: string, urlParams: any = {}, queryParams: any = {}): string {
    Object.keys(urlParams)
      .forEach(urlParam => url = url.replace(`{${urlParam}}`, urlParams[urlParam]));

    let query = Object.keys(queryParams)
      .map(k => `${encodeURIComponent(k)}=${encodeURIComponent(queryParams[k])}`)
      .join('&');
    query = query ? `?${query}` : '';

    url = url.replace(/{.+}/g, '');

    return `${url}${query}`;
  }

  public loadDocument(id: any): Observable<HtmlDocument> {
    // return this.http.get<HtmlDocument>(this.getUrl(this.config.documentsAPIUrl, {id}), {withCredentials: true});
    return this.http.get<HtmlDocument>(this.getUrl(this.configState.getConfig().documentsAPIUrl, {id}), {withCredentials: true});
  }

  public loadControls(documentId: string): Observable<Control[]> {
    // return this.http.get<Control[]>(this.getUrl(this.config.controlsAPIUrl, {}, {documentId}), {withCredentials: true});
    return this.http.get<Control[]>(this.getUrl(this.configState.getConfig().controlsAPIUrl, {}, {documentId}), {withCredentials: true});
  }

  public createControl(control: Control, documentId: string): Observable<Control[]> {
    // return this.http.post<Control[]>(this.getUrl(this.config.controlsAPIUrl, {}, {documentId}), control, {withCredentials: true});
    return this.http.post<Control[]>(this.getUrl(this.configState.getConfig().controlsAPIUrl, {}, {documentId}), control, {withCredentials: true});
  }

  public deleteControl(id: string, documentId: string): Observable<Control[]> {
    // return this.http.delete<Control[]>(this.getUrl(this.config.controlsAPIUrl, {id}, {documentId}), {withCredentials: true});
    return this.http.delete<Control[]>(this.getUrl(this.configState.getConfig().controlsAPIUrl, {id}, {documentId}), {withCredentials: true});
  }

  public updateControl(control: Control, documentId: string): Observable<Control[]> {
    return this.http
      // .patch<Control[]>(this.getUrl(this.config.controlsAPIUrl, {id: control.id}, {documentId}), control, {withCredentials: true});
      .patch<Control[]>(this.getUrl(this.configState.getConfig().controlsAPIUrl, {id: control.id}, {documentId}), control, {withCredentials: true});
  }


  public loadBlocks(documentId: string): Observable<CustomBlock[]> {
    // return this.http.get<Control[]>(this.getUrl(this.config.customBlocksAPIUrl, {}, {documentId}), {withCredentials: true});
    return this.http.get<Control[]>(this.getUrl(this.configState.getConfig().customBlocksAPIUrl, {}, {documentId}), {withCredentials: true});
  }

  public createBlock(block: CustomBlock, documentId: string): Observable<CustomBlock[]> {
    // return this.http.post<CustomBlock[]>(this.getUrl(this.config.customBlocksAPIUrl, {}, {documentId}), block, {withCredentials: true});
    return this.http.post<CustomBlock[]>(this.getUrl(this.configState.getConfig().customBlocksAPIUrl, {}, {documentId}), block, {withCredentials: true});
  }

  public deleteBlock(id: string, documentId: string): Observable<CustomBlock[]> {
    // return this.http.delete<CustomBlock[]>(this.getUrl(this.config.customBlocksAPIUrl, {id}, {documentId}), {withCredentials: true});
    return this.http.delete<CustomBlock[]>(this.getUrl(this.configState.getConfig().customBlocksAPIUrl, {id}, {documentId}), {withCredentials: true});
  }

  public updateBlock(control: CustomBlock, documentId: string): Observable<CustomBlock[]> {
    return this.http
      .patch<CustomBlock[]>(
        // this.getUrl(this.config.customBlocksAPIUrl, {id: control.customBlockId}, {documentId}), control, {withCredentials: true}
        this.getUrl(this.configState.getConfig().customBlocksAPIUrl, {id: control.customBlockId}, {documentId}), control, {withCredentials: true}
      );
  }
}
