import {HtmlObject} from '../../../../../../../ui-editor-common/entities/HtmlObject';
import {NodeUtils} from '../../../../../../../ui-editor-common/tools/NodeUtils';
import {IterableControlValueAccessor} from '../../../../entities/UIObject';
import {DomNodeAccessor} from './DomNodeAccessor';

export class GroupDomNodeAccessor extends DomNodeAccessor implements IterableControlValueAccessor {

  constructor(node: HtmlObject) {
    super(node);
  }


  setValue(valuePath: string, value: HtmlObject): void {
    if (valuePath) {
      const domNodes = (<Element>this.getDomNode()).querySelectorAll(valuePath);
      if (domNodes) {
        Array.from(domNodes).forEach(node => {
          const oldNode = this.getNodeByDomNode(node);
          value.path = oldNode.path;
          NodeUtils.reIndexNodeChildren(value);
          Object.assign(oldNode, value);
        });
      }

      return;
    }

    super.setValue(valuePath, value);
  }


}
