import {BaseValueAccessor} from './BaseValueAccessor';


export class FormValueAccessor extends BaseValueAccessor {

  private key;
  private value;

  public transformer = true;

  constructor(value: any = {}) {
    super();
    this.key = value.key;
    this.value = value.value;
  }

  getRawValue(): any {
    //todo delete this
    const value = {[this.key]: this.value};
    this.key = undefined;
    this.value = undefined;
    return value;
  }

  getValue(valuePath: string): any {
    return this[valuePath] || '';
  }

  setValue(valuePath: string, value: any): any {
    this[valuePath] = value;
  }

  public isValid() {
    return this.key !== undefined && this.value !== undefined;
  }

}

