import {UINodeComponent} from '../../../ui-node-component/ui-node.component';
import {ChangeDetectionStrategy, Component, DoCheck, EventEmitter, HostBinding, HostListener, OnInit} from '@angular/core';
import {
  ControlObject,
  ControlUpdatedEvent,
  ControlUpdateType,
  ControlValueAccessor,
  InputType,
  IterableControlValueAccessor,
  SettingsObject,
  UINodeEvent,
  UINodeEventType,
  UIObject,
  UpdateSummary,
} from '../../../../../entities/UIObject';
import {valueAccessors} from '../../value-accessors/valueAccessors';
import {cloneDeep, isEqual, pickBy, set} from 'lodash';
import {ControlUtils} from '../../../../../tools/ControlUtils';
import {DataMethod} from '../../../../../../../../ui-editor-common/services/dom.data.service';
import {EditorEventType} from '../../../../../entities/EditorEvent';
import {LogMethod} from '../../../../../tools/decorators/Log/Log.decorator';
import {EventsService} from '../../../../../state/events.service';

/**
 * Базовий компонент відображення контрола
 */
@Component({
  selector: 'base-control',
  templateUrl: '../../../ui-node-component/ui-node.component.html',
  styleUrls: ['./base-control.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
// @LogClass()
export class BaseControlComponent extends UINodeComponent implements OnInit, DoCheck {

  @HostBinding('class.control-with-inputs') containsInputs: boolean;

  valueAccessor: ControlValueAccessor;
  node: ControlObject;
  uiNodeEvent: EventEmitter<ControlUpdatedEvent> = new EventEmitter<ControlUpdatedEvent>();

  ngOnInit(): void {
    super.ngOnInit();
    this.setValueAccessor();
    this.containsInputs = Boolean(this.node.children.find(c => Object.values(InputType).includes(<InputType>c.tag)));
  }

  ngDoCheck(): void {
    if (!isEqual(this.node, this.container.node)) {
      this.node = <ControlObject>this.container.node;
      this.node.localState && Object.assign(this.localState, this.node.localState);
      this.setValueAccessor();
    }
    if (this.node && !isEqual(this.node.localState, this.localState)) {
      this.updateLocalState();
    }
  }

  //todo causes change detection on each run
  @HostListener('click', ['$event'])
  onClick(e: Event) {
    if (!this.node.path.length && e['ctrlKey']) {
      this.applicationState = {
        editedControl: this.node,
        addingControl: true
      };
    }
  }

  /**
   * Створює valueAccessor за ім'ям з проперті "node.valueAccessor"
   */
  protected setValueAccessor(): void {
    // @ts-ignore
    this.valueAccessor = new valueAccessors[this.node.valueAccessor](this.node.value);
  }

  /**
   * Сетить потрібні проперті в чайлда за допомогою valueAccessor
   */
  protected applyChildBinding(child: SettingsObject, to: string, from: string, context: any = this): UIObject {
    // todo BUG child should be new every time
    // todo maybe predefined value should be in separate prop, otherwise we do not know which value should be removed
    // if (child[to] !== undefined && this.rendererMode === UIRendererMode.DISPLAY) {
    //   return child;
    // }

    if (!child.iterate && from && from.indexOf(ControlUtils.valueAccessorProp) === 0) {
      set(child, to, cloneDeep(this.valueAccessor.getValue(ControlUtils.getValueAccessorProperty(from))));
      return child;
    } else {
      return super.applyChildBinding(child, to, from, context);
    }
  }

  protected updateValue(property: string, value: any): void {
    this.valueAccessor.setValue(ControlUtils.getValueAccessorProperty(property), value);
  }

  protected insertValue(value: any): void {
    (<IterableControlValueAccessor>this.valueAccessor).addItem(value);
  }

  protected removeValue(index: string | number): void {
    (<IterableControlValueAccessor>this.valueAccessor).removeItem(index);
  }

  // todo global state is being set here dynamically
  /**
   * Якщо чайлд елемент зв'язаний зі сторою, то діспатчить івент на зміну стори
   * якщо ні, то змінює значення проперті парента
   * @param child
   */
  protected updateBindedProperty(child: SettingsObject): void {
    console.log('child.bindings', child.bindings);
    if (child.bindings.value.match('applicationState')) {
      EventsService.dispatch({
        type: EditorEventType.UPDATE_APP_STATE,
        data: {
          [child.bindings.value.replace(`applicationState.`, '')]: child.value
        }
      });
    } else {
      set(this, child.bindings.value, child.value);
    }
    this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
  }

  protected getDataMethod(updateType: ControlUpdateType): DataMethod {
    if (updateType === ControlUpdateType.MODIFY || !this.valueAccessor.structural) {
      return DataMethod.UPDATE;
    }

    if (updateType === ControlUpdateType.INSERT) {
      return DataMethod.INSERT;
    }

    if (updateType === ControlUpdateType.DELETE) {
      return DataMethod.DELETE;
    }
  }

  protected getUpdateSummary(event: ControlUpdatedEvent): UpdateSummary {
    const dataMethod = this.getDataMethod(event.emitter.updateType);
    const lastSummary = event.summary || {} as any;

    if (this.valueAccessor.transformer) {
      return event.summary || {dataMethod} as any;
    }

    return {
      modifiedPath: this.valueAccessor.getModifiedPath && this.valueAccessor.getModifiedPath(),
      modifiedProperty: this.node.bindings.value,
      value: this.valueAccessor.getModifiedValue(),
      ...pickBy(lastSummary, v => !!v),
      dataMethod: dataMethod !== DataMethod.UPDATE ? dataMethod : (lastSummary.dataMethod || dataMethod)
    };

  }

  protected save(event: ControlUpdatedEvent): void {
    if (this.valueAccessor.isValid()) {
      this.uiNodeEvent.emit({
        type: UINodeEventType.CONTROL_VALUE_UPDATE,
        emitter: {...this.node, value: this.valueAccessor.getRawValue()},
        summary: this.getUpdateSummary(event)
      });
    }
  }

  public onChildUpdated(event: ControlUpdatedEvent): void {
    if (event.emitter.updateType === ControlUpdateType.INSERT) {
      this.insertValue(event.emitter.value);
    } else if (event.emitter.updateType === ControlUpdateType.MODIFY) {
      this.updateValue(event.emitter.bindings.value, event.emitter.value);
    }
  }

  public onIterableChildUpdated(event: ControlUpdatedEvent, iterableComponentIndex: string | number): void {
    if (event.emitter.updateType === ControlUpdateType.DELETE) {
      this.removeValue(iterableComponentIndex);
    } else {
      (<IterableControlValueAccessor>this.valueAccessor).updateItem(iterableComponentIndex, event.emitter.value);
    }
  }

  private isControlValueUpdated(event: ControlUpdatedEvent): boolean {
    return event.emitter.updateType === ControlUpdateType.DELETE ||
      event.emitter.updateType === ControlUpdateType.INSERT ||
      ControlUtils.isBoundToValueAccessor(event.emitter.bindings.value);
  }

  private updateControlValue(event: ControlUpdatedEvent, iterableComponentIndex?: string | number): void {
    if (event.emitter.updateType !== ControlUpdateType.NONE) {
      iterableComponentIndex !== undefined ?
        this.onIterableChildUpdated(event, iterableComponentIndex) :
        this.onChildUpdated(event);
    }
    this.save(event);
  }

  protected onControlUpdate(event: UINodeEvent, iterableComponentIndex?: string | number): void {
    if (iterableComponentIndex !== undefined || this.isControlValueUpdated(event)) {
      this.updateControlValue(event, iterableComponentIndex);
    } else {
      this.updateBindedProperty(event.emitter);
    }
  }

  public onComponentEvent(event: UINodeEvent, iterableComponentIndex?: string | number): void {
    console.log('onComponentEvent', event, iterableComponentIndex);
    if (event.type === UINodeEventType.CONTROL_VALUE_UPDATE) {
      this.onControlUpdate(event, iterableComponentIndex);
    }
  }

  // todo duplication from basecontroleditsettings
  /**
   * Встановлює чайлдам значення з bindings({key: value}).
   * Таким чином для кожного child.key буде значення this[value] aбо this.valueAccessor[value] (якщо value починається з 'valueAccessor')
   * @param child
   * @param context
   */
  public applyBindings(child: SettingsObject, context: any = this): SettingsObject {
    const result: SettingsObject = super.applyBindings(child, context);
    if (result.localState && result.localState.initialValue) {
      result.value = result.localState.initialValue;
    }
    return result;
  }
}
