import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {RepositoryService} from './api/repository.service';
import {WebsocketService} from './api/websocket.service';
import {applicationUI} from './configs/applicationUI';
import {fakeConfig} from './fake-config';
import {EventsService} from './state/events.service';
import {StateStream} from './tools/decorators/StateStream/StateStream.decorator';
import {EditorEventListener} from './tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {UIState} from './state/ui-state.service';
import {ExtraModuleInjector} from './services/extra-module-injector/extra-module-injector';
import {Observable} from 'rxjs';
import {HtmlDocument} from '../../common/entities/HtmlDocument';
import {Emitter} from './tools/decorators/Emitter/Emitter.decorator';
import {EditorEventType} from './entities/EditorEvent';
import {getFakeDocument} from './configs/fakeDocument';
import {StateManagerService} from './state/state-manager.service';
import {DocumentStateService} from './state/document.state.service';
import {ConfigStateService} from './state/config.state.service';
import {AppStateService} from './state/app.state.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less'],
  encapsulation: ViewEncapsulation.ShadowDom
})
@EditorEventListener()
export class AppComponent implements OnInit {
  @StateStream({stateProperty: 'ui'}) ui$;

  public constructor(emi: ExtraModuleInjector,
                     private repository: RepositoryService,
                     private ws: WebsocketService,
                     private state: StateManagerService,
                     private documentState: DocumentStateService,
                     private events: EventsService,
                     private configState: ConfigStateService,
                     private uiState: UIState,
                     private appState: AppStateService) {
  }

  /**
   * Завантажує документ з репозіторію, після отримання діспатчить екшн SET_DOCUMENT для запису його в стор
   */
  @Emitter({type: EditorEventType.SET_DOCUMENT})
  private loadDocument(): Observable<HtmlDocument> {
    const documentId = window.location.pathname.replace('/', '') || '1';
    return this.repository.loadDocument(documentId);
  }

  @Emitter({type: EditorEventType.SET_DOCUMENT})
  private loadFakeDocument() {
    return getFakeDocument();
  }

  private setUI() {
    this.uiState.setUI(applicationUI);
  }

  private setConfig(): void {
    this.configState.setConfig({
      externalBlocks: [],
      externalControls: [],
      documentsAPIUrl: 'http://localhost:5000/documents/{id}',
      controlsAPIUrl: 'http://localhost:5000/controls/{id}',
      customBlocksAPIUrl: 'http://localhost:5000/custom-blocks/{id}',
      // ...(this.el.nativeElement.config || {})
      ...(fakeConfig || {}),
      useFakeData: true,
    });
  }

  private setAppState(): void {
    this.appState.set({
      addingControl: false,
      editedControl: undefined,
      viewingHistory: false,
      selectedNode: undefined,
      draggedNode: undefined,
      draggedControl: undefined,
      selectedRemoteNodes: [],
      previewFrame: undefined,
    });
  }

  ngOnInit(): void {
    console.log('%cEditor has started!', 'color:green;font-weight:bold;', this);
    this.setAppState();
    this.setUI();
    this.setConfig();
    console.log("this.configState.config",this.configState.config)
    this.configState.config.useFakeData ? this.loadFakeDocument() : this.loadDocument();
  }
}

