import {NodeComponent, NodeComponentNode} from '../../../entities/NodeComponent';
import {BaseDomRenderer} from './base.dom-renderer';
import {ChangeDetectorRef, ElementRef, HostBinding, Injector, OnDestroy, OnInit} from '@angular/core';
import {filter} from 'rxjs/operators';
import {HtmlObject} from '../../../../../../ui-editor-common/entities/HtmlObject';
import {EditorEvent, EditorEventType} from '../../../entities/EditorEvent';
import {EventsService} from '../../../state/events.service';
import {Path} from '../../../../../../ui-editor-common/entities/Path';
import {NodeUtils} from '../../../../../../ui-editor-common/tools/NodeUtils';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {EventSubscriber} from '../../../tools/decorators/Subscriber/Subscriber.decorator';
import {InjectedValue} from '../../../tools/decorators/InjectedValue/InjectedValue.decorator';
import {AsyncValue} from '../../../tools/decorators/AsyncValue/AsyncValue.decorator';
import {StateStream} from '../../../tools/decorators/StateStream/StateStream.decorator';
import {ApplicationState} from '../../../state/state.types';


/**
 * Базовий компонент відображення ноди, реалізує {@link NodeComponent}
 * <br/> підписується на івенти drag & drop зі стора і додає відповідні класи компоненту
 */
@EditorEventListener()
export class BaseNodeComponent implements NodeComponent, OnInit, OnDestroy {
    @HostBinding('class.dragged-node-component') dragged = false;
    @HostBinding('class.dragged-over-node-component') draggedOver = false;
    @HostBinding('class.dragged-over-insert-before-node-component') draggedOverInsertBefore = false;
    draggable: boolean = false;
    @InjectedValue({token: NodeComponentNode, dynamicInjector: true}) node: HtmlObject;
    container: BaseDomRenderer;
    index: number;


    @AsyncValue()
    @StateStream({stateProperty: 'applicationState'})
    // @ts-ignore
    public applicationState: Partial<ApplicationState>/* = this.applicationState*/;

    // applicationState = {};

    constructor(public cdr: ChangeDetectorRef,
                protected el: ElementRef,
                protected events: EventsService,
                protected injector: Injector) {
    }

    protected applyNodeAttributes() {
        this.node && Object.keys(this.node.attributes || {})
            .forEach(attr => this.el.nativeElement.setAttribute(attr, this.node.attributes[attr]));
    }

    ngOnInit(): void {
        // console.log('%cINIT', 'font-weight:bold;color:red;');
        this.applyNodeAttributes();
        // if (!this.applicationState.componentsMap) {
        //   this.applicationState = {componentsMap: new WeakMap<Element, BaseNodeComponent>()};
        // }
        // this.applicationState.componentsMap.set(this.el.nativeElement, this);
    }

    // @LogMethod()
    @EventSubscriber({type: EditorEventType.NODE_DRAG_START, pipes: 'isOverCurrentNode', condition: 'draggable'})
    protected onDragStart() {
        console.log('onDragStart');
        setTimeout(() => {
            this.dragged = true;
            this.cdr.markForCheck();
        });
    }

    @EventSubscriber({type: EditorEventType.NODE_DRAG_LEAVE, pipes: 'isOverCurrentNode', condition: 'draggable'})
    @EventSubscriber({type: EditorEventType.NODE_DRAG_END, condition: 'draggable'})
    protected onDragStop(): void {
        this.dragged = false;
        this.draggedOver = false;
        this.draggedOverInsertBefore = false;
        this.cdr.markForCheck();
    }

    @EventSubscriber({type: EditorEventType.NODE_DRAG_ENTER, pipes: 'isOverCurrentNode', condition: 'draggable'})
    protected onDragEnter(e: EditorEvent) {
        if (e.data.ctrlKey) {
            this.draggedOverInsertBefore = true;
        } else {
            this.draggedOver = true;
        }
        this.cdr.markForCheck();
    }

    protected moveInto(e: Event): void {
    }

    protected copyInto(e: Event): void {
    }

    // @LogMethod()
    @EventSubscriber({type: EditorEventType.NODE_DRAG_DROP, pipes: 'isOverCurrentNode', condition: 'draggable'})
    protected onDrop(e: EditorEvent): void {
        console.log('BNC onDrop:', e);
        e.data.altKey ? this.copyInto(e.data) : this.moveInto(e.data);
        this.cdr.markForCheck();
    }

    // protected isOverCurrentNode() {
    //     return filter((e: EditorEvent) => {
    //         return e.data.target === this.el.nativeElement;
    //     });
    // }

    protected isOverCurrentNode(e: EditorEvent): boolean {
        return e.data.target === this.el.nativeElement;
    }


    protected getPathToInsert(previousPath: Path, e: Event, move = false): Path {
        const targetPath = [...this.node.path];
        if (move &&
            NodeUtils.areSiblings([this.node.path, previousPath]) &&
            NodeUtils.isBefore(previousPath, this.node.path)) {
            targetPath[targetPath.length - 1]--;
        }
        return e['ctrlKey'] ? targetPath : [...targetPath, this.node.children.length];
    }

    ngOnDestroy(): void {
        delete this.el;
    }
}
