import {ChangeDetectorRef, Component, ElementRef, Injector, OnInit} from '@angular/core';
import {EventsService} from '../../../state/events.service';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {ActionsService} from '../../../domain/actions.service';
import {UINodeComponent} from '../ui-node-component/ui-node.component';
import {BehaviorableNode} from '../../../entities/BehaviorableNode';
import {Action} from '../../../../../../ui-editor-common/entities/Action';
import {EditorEvent, EditorEventType} from '../../../entities/EditorEvent';
import {EventSubscriber, StateSubscriber} from '../../../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {LogClass} from '../../../tools/decorators/Log/Log.decorator';

/**
 * Компонет відображення html коду потомок {@link UINodeComponent}
 * використовує {@link HtmlEditorComponent}
 */
@Component({
  selector: 'code-editor',
  templateUrl: './code-editor.component.html',
  styleUrls: ['./code-editor.component.less']
})
@EditorEventListener()
// @LogClass()
export class CodeEditorComponent extends UINodeComponent implements OnInit {
  private editedNode: BehaviorableNode;

  public constructor(events: EventsService,
                     private actionManager: ActionManagerService,
                     private actions: ActionsService,
                     el: ElementRef,
                     cdr: ChangeDetectorRef,
                     injector: Injector) {
    super(cdr, el, events, injector);
  }

  ngOnInit(): void {
  }

  @StateSubscriber({stateProperty: 'applicationState.selectedNode'})
  private onNodeSelected(node: BehaviorableNode) {
    this.editedNode = node;
  }

  // @LogMethod({logTrace: true})
  public updateNode([updatedNode, ...newNodes]) {
    const oldNode = this.editedNode.getHtmlObjectValue();
    const actions: Action[] = [
      this.actions.getNodeUpdateAction(oldNode, oldNode.path, null, updatedNode)
    ];
    newNodes && newNodes.length && actions.push(
      this.actions.getNodeInsertAction(newNodes[0].path, newNodes)
    );

    this.actionManager.runActions(actions);
  }

}
