import {Injectable} from '@angular/core';
import {HtmlObject} from '../../../../common/entities/HtmlObject';
import {BehaviorableNode} from '../../entities/BehaviorableNode';
import {ControlsService} from '../controls/controls.service';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {EventsService} from '../../state/events.service';
import {EditorEventType} from '../../entities/EditorEvent';

/**
 * Сервіс, що на селект [ноди] {@link HtmlObject} перетворює її в
 * BehaviorableNode(містить в собі конфіг з потрібними контролами) і діспатчить UPDATE_APP_STATE з selectedNode = BehaviorableNode
 */
@Injectable()
export class NodeWrapperService {

  constructor(private controls: ControlsService) {
  }

  public getBehaviorableNode(node: HtmlObject): Observable<BehaviorableNode> {
    return this.controls
      .getControls(node.customBlockId)
      .pipe(
        map((controls) => new BehaviorableNode(node, {controls}))
      );
  }

  public selectNode(node: HtmlObject): void {
    this.getBehaviorableNode(node)
      .subscribe(n => EventsService.dispatch({
        type: EditorEventType.UPDATE_APP_STATE,
        data: {selectedNode: n}
      }));
  }
}
