import {HtmlNodeType, HtmlObject} from '../../../../../../../ui-editor-common/entities/HtmlObject';
import {BaseValueAccessor} from './BaseValueAccessor';
import {Path} from '../../../../../../../ui-editor-common/entities/Path';


export class TextValueAccessor extends BaseValueAccessor {
  private node: HtmlObject;
  private text: HtmlObject;

  constructor(node: HtmlObject) {
    super();
    if (node) {
      this.node = node;
      if (this.node.children[0] && this.node.children[0].type === HtmlNodeType.TEXT_NODE) {
        this.text = this.node.children[0];
      } else {
        this.createText();
      }
    }
  }

  private createText(): void {
    this.node.children[0] = {
      type: HtmlNodeType.TEXT_NODE, attributes: undefined, children: [], content: '', path: [], tag: '',
    };
    this.text = this.node.children[0];
  }

  getRawValue(): HtmlObject {
    return this.node;
  }

  getValue(valuePath: string): string {
    if (this.text) {
      return this.text.content;
    }
  }

  setValue(valuePath: string, value: any): void {
    if (this.text) {
      this.text.content = value;
    }
  }

  public getModifiedPath(): Path {
    return this.text.path;
  }

  getModifiedValue() {
    return this.text;
  }

}
