  //   // while (lastIndex < path.length - 1) {
  //   //   if (path[lastIndex] !== dependable[lastIndex]) {
  //   //     return false;
  //   //   }
  //   //   lastIndex++;
  //   // }
  //   //
  //   // return path[lastIndex] <= dependable[lastIndex];
