

import {Component} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {StateSubscriber} from '../../../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';

/**
 * Відображає document.css зі стора
 */
@Component({
  selector: 'css-renderer',
  template: '<div class="service-element" [innerHTML]="styles"></div>',
})
@EditorEventListener()
// @LogClass({logTrace: true})
export class CssRendererComponent {
  public styles: any = '';

  public constructor(private sanitizer: DomSanitizer) {
    console.log("\n\nCSS:",this)
  }

  /**
   * Підписується на 'document.css' і записує в this.styles у вигляді html
   */
  @StateSubscriber({stateProperty: 'document.css'})
  onDataUpdated(css) {
    console.log("\n\nonDataUpdated",css)
    if (css) {
      this.styles = this.sanitizer.bypassSecurityTrustHtml(`
            <style>
                ${css.textValue}
            </style>
        `);
    }
  }
}
