import {isObservable, Observable} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {destroyEmitter} from '../decorator.types';
import DecoratorTools from '../decorator.tools';

export function AsyncValue(property?: string) {
    return function (target: Object, key: string) {

        let value;
        target[key].subscribe(v => value = property ? v[property] : v);

        // DecoratorTools.redefineAccessors(target, key, (_get, _set) => {
        //   return {
        //     get: function () {
        //       return _get.call(this);
        //     },
        //     set: function (value: Observable<any>) {
        //       if (isObservable(value)) {
        //         value
        //           .pipe(takeUntil(this[destroyEmitter]))
        //           .subscribe(result => result && _set.call(this, property ? result[property] : result, true));
        //       } else {
        //         _set.call(this, value);
        //       }
        //     }
        //   };
        // });

        DecoratorTools.redefineAccessors(target, key, (_get, _set) => {
            return {
                get: function () {
                    return value;
                },
                set: function (value: Observable<any>) {
                    _set.call(this, value);
                }
            };
        });
    };
}
