import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostBinding,
  HostListener,
  Injector,
  OnDestroy,
  OnInit
} from '@angular/core';
import {DocumentRendererComponent} from '../../renderers/dom/document/document-renderer.component';
import {EventsService} from 'src/app/state/events.service';
import {ActionsService} from '../../../domain/actions.service';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {NodeUtils} from '../../../../../../ui-editor-common/tools/NodeUtils';
import {BaseNodeComponent} from '../../renderers/dom/base.node-component';
import {HtmlNodeType, HtmlObject} from '../../../../../../ui-editor-common/entities/HtmlObject';
import {NodeWrapperService} from '../../../services/node-wrapper/node-wrapper.service';
import {CustomBlocksService} from '../../../services/custom-blocks/custom-blocks.service';
import {EditorEvent, EditorEventType} from '../../../entities/EditorEvent';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {EventSubscriber, StateSubscriber} from '../../../tools/decorators/Subscriber/Subscriber.decorator';

export const baseBlockSelector = '[document-node-component]';
const changeDetectionStrategy = ChangeDetectionStrategy.OnPush;

/**
 * Компонент відображення ноди документа (імейла), потомок {@link BaseNodeComponent}
 * <br/> підписується на івенти руху миші зі стора і додає відповідні класи компоненту
 */
@Component({
  selector: baseBlockSelector,
  templateUrl: './document-node.component.html',
  styleUrls: ['./document-node.component.less'],
  //encapsulation: ViewEncapsulation.ShadowDom
})
@EditorEventListener()
// @LogClass()
export class DocumentNodeComponent extends BaseNodeComponent implements OnInit, OnDestroy {
  @HostBinding('attr.draggable') draggable = true;

  nodeTypes = HtmlNodeType;
  node: HtmlObject;
  container: DocumentRendererComponent;
  showSaveForm = false;

  @HostBinding('class.document-node-component')
  public documentNode = true;

  @HostBinding('class.hovered-node-component')
  public hovered = false;

  @HostBinding('class.selected-node-component')
  public selected = false;

  @HostBinding('style.box-shadow')
  public selectedRemotelyStyle: string;

  public remoteSelectionData: any;

  public constructor(el: ElementRef,
                     events: EventsService,
                     protected actionManager: ActionManagerService,
                     cdr: ChangeDetectorRef,
                     protected actions: ActionsService,
                     private nodeWrapper: NodeWrapperService,
                     private customBlocks: CustomBlocksService,
                     injector: Injector) {
    super(cdr, el, events, injector);
  }

  @StateSubscriber({stateProperty: 'document'})
  onDocumentUpdated() {
    this.cdr.markForCheck();
  }

  protected isNewNode(node: HtmlObject): boolean {
    return !this.applicationState.draggedNode.path || !this.applicationState.draggedNode.path.length;
  }

  protected onDragStart() {
    this.applicationState = {draggedNode: this.node};
    super.onDragStart();
  }

  protected moveInto(e: Event): void {
    this.actionManager.runActions([
      this.actions.getNodeMoveAction(
        this.applicationState.draggedNode.path,
        this.getPathToInsert(this.applicationState.draggedNode.path, e, true)
      )
    ]);
    this.onDragStop();
  }

  protected onDragStop(): void {
    super.onDragStop();
  }

  protected onDrop(e: EditorEvent): void {
    if (this.isNewNode(this.applicationState.draggedNode)) {
      this.copyInto(e.data);
    } else {
      super.onDrop(e);
    }
  }

  protected copyInto(e: Event): void {
    this.actionManager.runActions([
      this.actions.getNodeInsertAction(
        this.getPathToInsert(this.applicationState.draggedNode.path, e), [this.applicationState.draggedNode]
      )
    ]);
    this.onDragStop();
  }

  // @LogMethod()
  ngOnInit(): void {
    super.ngOnInit();
    this.setNodeSelection();
  }

  @EventSubscriber({type: EditorEventType.MOUSE_OUT_NODE})
  private onHoveOut(e: EditorEvent) {
    const el = this.el.nativeElement;
    if (this.hovered && e.data.toElement !== el &&
      (e.data.relatedTarget ? !e.data.relatedTarget.classList.contains('service-element') : true)) {
      this.hovered = false;
      this.cdr.markForCheck();
    }
  }

  @EventSubscriber({type: EditorEventType.MOUSE_OVER_NODE, pipes: 'isOverCurrentNode'})
  private onHover(e: EditorEvent) {
    this.hovered = true;
    this.cdr.markForCheck();
  }

  @EventSubscriber({type: EditorEventType.REMOTE_NODE_SELECTED})
  private setRemoteNodesNodeSelection(): void {
    for (const n of this.applicationState.selectedRemoteNodes) {
      if (NodeUtils.arePathsEqual(this.node.path, n.node.path)) {
        this.selectedRemotelyStyle = `inset 0px 0px 0px 2px ${n.color}`;
        this.remoteSelectionData = n;
        return;
      }
    }
    this.remoteSelectionData = null;
    this.selectedRemotelyStyle = ``;
  }

  @StateSubscriber({stateProperty: 'applicationState.selectedNode'})
  private setNodeSelection(): void {
    this.selected = this.applicationState.selectedNode && NodeUtils.arePathsEqual(this.node.path, this.applicationState.selectedNode.path);
    if (!this.selected) {
      this.showSaveForm = false;
      this.cdr.markForCheck();
    }
  }

  // todo replace with single preview panel event
  @HostListener('click', ['$event'])
  onClick(e) {
    e.stopPropagation();
    this.nodeWrapper.selectNode(this.node);
  }

  public removeNode(): void {
    console.log('removeNode');
    this.actionManager.runActions([
      this.actions.getNodeDeleteAction(this.node.path, [this.node])
    ]);
  }

  public saveNode(id: string): void {
    this.showSaveForm = false;
    this.customBlocks.saveCustomBlock(id, this.node);
  }

  ngOnDestroy(): void {
  }
}

