import {ChangeDetectionStrategy, Component, EventEmitter, OnDestroy, OnInit} from '@angular/core';
import {BaseNodeComponent} from '../../renderers/dom/base.node-component';
import {
  ConditionConnection,
  IterableValueItem,
  UINodeEvent,
  UIObject,
  UIRendererMode
} from '../../../entities/UIObject';
import {UIRendererComponent} from '../../renderers/dom/ui/ui-renderer.component';
import {cloneDeep, get, set} from 'lodash';
import {LogicalOperator} from '../../../tools/LogicalOperator';

export const baseUIComponentSelector = '[ui-node-component]';

/**
 * Базовий компонент відображення ноди UI, потомок {@link BaseNodeComponent}
 * <br/> підписується на івенти drag & drop і додає відповідні класи компоненту
 * відображає усі чайлди [node]{@link UIObject} за допомогою {@link UIRendererComponent}
*/
@Component({
  selector: baseUIComponentSelector,
  templateUrl: './ui-node.component.html',
  styleUrls: ['./ui-node.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UINodeComponent extends BaseNodeComponent implements OnInit, OnDestroy {

  public id = Math.random();
  public localState: any = {};

  node: UIObject;
  container: UIRendererComponent;
  uiNodeEvent: EventEmitter<UINodeEvent> = new EventEmitter<UINodeEvent>();
  rendererMode = UIRendererMode.DISPLAY;

  ngOnInit(): void {
    super.ngOnInit();
    if (this.node) {
      this.updateLocalState();
      Object.keys(this.node.attributes || []).forEach(attr => this.el.nativeElement.setAttribute(attr, this.node.attributes[attr]));
    }
  }

  protected updateLocalState() {
    this.node.localState && Object.assign(this.localState, this.node.localState);
  }

  protected applyChildBinding(child: UIObject, to: string, from: string, context: any = this): UIObject {
    if (child.iterate && !from) {
      set(child, to, cloneDeep(context));
    } else {
      set(child, to, cloneDeep(get(context, from)));
    }
    return child;
  }


  public applyBindings(child: UIObject, context: any = this): UIObject {
    child = cloneDeep(child);
    if (child.bindings) {
      Object
        .entries(child.bindings)
        .forEach(([to, from]) => child = this.applyChildBinding(child, to, from, context));
    }
    return child;
  }

  // todo override in control to check value accessor as well
  /**
   * Перевірка чи потрібно відображати child за умовами в if блоці
   * @param child
   */
  public isChildDisplayed(child: UIObject): boolean {
    if (!child.if) {
      return true;
    }

    let result;

    for (const condition of child.if) {

      const value1 = get(this, condition.prop1);
      const value2 = condition.prop2 ? get(this, condition.prop2) : condition.value;

      if (!condition.connection) {
        result = LogicalOperator[condition.comparison](value1, value2);
      } else if (condition.connection === ConditionConnection.OR && result) {
        return result;
      } else {
        result = LogicalOperator[condition.connection](result, LogicalOperator[condition.comparison](value1, value2));
      }
    }
    return result;
  }

  public onComponentEvent(event: UINodeEvent, iterableComponentIndex?: string | number): void {
  }

  // todo CAREFUL MAY MESS UP THINGS

  public trackByPath(index: number, item: UIObject) {
    return `${index}_${item.path.join(',')}`;
  }

  public trackByKey(index: number, item: IterableValueItem) {
    return `${index}_${item.key}`;
  }

  ngOnDestroy(): void {
  }
}




