import {IterableValueItem, IterableControlValueAccessor} from '../../../../entities/UIObject';
import {HtmlConverter} from '../../../../tools/HtmlConverter';
import {BaseValueAccessor} from './BaseValueAccessor';

export class StyleValueAccessor extends BaseValueAccessor implements IterableControlValueAccessor {
  styleObject: any;

  constructor(styleValue: string) {
    super();
    if (typeof styleValue !== 'string') {
      styleValue = '';
    }
    this.styleObject = HtmlConverter.stylesToObject(styleValue);
  }

  getRawValue(): string {
    return Object.keys(this.styleObject).map(k => `${k}:${this.styleObject[k]}`).join(';');
  }

  getValue(valuePath: string): any {
    return this.getItem(valuePath);
  }

  setValue(valuePath: string, value: any): any {
    this.styleObject[valuePath] = value;
    if (!this.styleObject[valuePath]) {
      delete this.styleObject[valuePath];
    }
  }

  [Symbol.iterator](): Iterator<IterableValueItem> {
    let pointer = 0;
    const iterable = this;
    return {
      next(value?: any): IteratorResult<any> {
        if (pointer < Object.keys(iterable.styleObject).length) {
          const result = {
            done: false,
            value: {
              key: Object.keys(iterable.styleObject)[pointer],
              value: iterable.styleObject[Object.keys(iterable.styleObject)[pointer]]
            } as IterableValueItem
          };
          pointer++;
          return result;
        } else {
          return {
            done: true,
            value: null
          };
        }
      }
    };
  }


  updateItem(valuePath: string, value: any): void {
    Object.assign(this.styleObject, value);
    if (!this.styleObject[valuePath]) {
      delete this.styleObject[valuePath];
    }
  }


  getItem(valuePath: string): string {
    return this.styleObject[valuePath];
  }

  addItem(item: any): any {
    Object.assign(this.styleObject, item);
  }

  removeItem(valuePath: string): any {
    delete this.styleObject[valuePath];
  }


}
