import {HtmlAttributes, HtmlNodeType, HtmlObject} from '../../../common/entities/HtmlObject';
import {BlockConfig} from './UIObject';
import {cloneDeep} from 'lodash';

// @LogClass()
export class BehaviorableNode implements HtmlObject {
  private nodeKeys;

  tag: string;
  type: HtmlNodeType;
  attributes: HtmlAttributes;
  children: HtmlObject[];
  path: number[];
  config: BlockConfig;

  public constructor(node: HtmlObject, config: BlockConfig) {
    node = cloneDeep(node);
    this.nodeKeys = Object.keys(node);
    this.nodeKeys.forEach(k => this[k] = node[k]);
    this.config = cloneDeep(config);
  }

  // @LogMethod()
  public getHtmlObjectValue(): HtmlObject {
    const object = {} as HtmlObject;
    this.nodeKeys.forEach(k => object[k] = this[k]);
    return object;
  }
}
