import {
  ChangeDetectorRef,
  Component,
  ComponentFactoryResolver, ComponentRef, EventEmitter,
  Injector,
  Input, OnChanges,
  OnDestroy,
  OnInit, Output, SimpleChanges,
  Type,
  ViewContainerRef
} from '@angular/core';
import {BaseDomRenderer} from '../base.dom-renderer';
import {BaseNodeComponent} from '../base.node-component';
import {UINodeEvent, UINodeEventType, UIObject, UIRendererMode} from '../../../../entities/UIObject';
import {editControlsMap, uiNodeComponents, uiNodeComponentsMap} from '../../../ui/ui-node-component/ui-node-components';
import {UINodeComponent} from '../../../ui/ui-node-component/ui-node.component';


const componentsMaps = {
  [UIRendererMode.EDIT]: editControlsMap,
  [UIRendererMode.DISPLAY]: uiNodeComponentsMap
};

/**
 * Відображає node типу {@link UIObject} в режимі {@link UIRendererMode}, дефолтний режим DISPLAY
 * в залежності від режиму використуває editControl або uiNodeComponent
 * <br/>На зміну значення емітить івент valueChanged з массивом нових {@link HtmlObject}
 * <br/>передає івенти на гору через componentEvent
 */
@Component({
  selector: 'ui-renderer',
  template: '',
  entryComponents: uiNodeComponents,
})
export class UIRendererComponent extends BaseDomRenderer implements OnInit, OnDestroy, OnChanges {

  private static UIComponentsRefs: ComponentRef<UINodeComponent>[] = [];

  @Input() node: UIObject;
  @Input() mode = UIRendererMode.DISPLAY;
  @Output() componentEvent = new EventEmitter<UINodeEvent>();
  component: UINodeComponent;

  constructor(injector: Injector,
              componentFactoryResolver: ComponentFactoryResolver,
              viewContainerRef: ViewContainerRef,
              public cdr: ChangeDetectorRef) {
    super(injector, componentFactoryResolver, viewContainerRef);
  }

  protected getComponentType(): Type<BaseNodeComponent> {
    return componentsMaps[this.mode][this.node.tag];
  }

  protected insertComponent(): void {
    super.insertComponent();
    UIRendererComponent.UIComponentsRefs.push(this.componentRef);
    // todo unsubscribe
    this.component.uiNodeEvent.subscribe(data => {
      if (data.type === UINodeEventType.CONTROL_PROPERTY_UPDATED) {
        this.detectChangesInAllUIComponents();
      }
      this.componentEvent.emit(data);
    });
  }

  protected removeComponent(): void {
    super.removeComponent();
    UIRendererComponent.UIComponentsRefs = UIRendererComponent.UIComponentsRefs.filter(r => r !== this.componentRef);
  }

  public detectChangesInAllUIComponents() {
    UIRendererComponent.UIComponentsRefs.forEach(r => r.instance.cdr.markForCheck());
  }

  ngOnChanges(changes: SimpleChanges): void {
  }
}
