import {ChangeDetectorRef, Component, ComponentFactoryResolver, ElementRef, Injector, ViewContainerRef} from '@angular/core';
import {UINodeComponent} from '../ui-node-component/ui-node.component';
import {EventsService} from '../../../state/events.service';
import {PreviewPanelComponent} from './preview-panel.component';
import {previewStyles} from './styles';
import {EditorEventType} from '../../../entities/EditorEvent';

/**
 * Відображає документ (email) в окремому iframe, спадкоємець {@link UINodeComponent}
 * мапиться на відображення по типу PREVIEW_CONTAINER {@link UIObjectType}
 * створює PreviewPanelComponent і додає його в body iframe
 */
@Component({
  selector: 'preview-container-component',
  template: '<iframe #frame (load)="onFrameLoad(frame)"></iframe>',
  styleUrls: ['./preview-panel-container.component.less'],
  entryComponents: [PreviewPanelComponent],
})
export class PreviewPanelContainer extends UINodeComponent {

  frameDocument: Document;

  constructor(injector: Injector,
              protected componentFactoryResolver: ComponentFactoryResolver,
              protected viewContainerRef: ViewContainerRef,
              el: ElementRef,
              cdr: ChangeDetectorRef,
              events: EventsService) {
    super(cdr, el, events, injector);
  }


  // TODO store required scripts in head of document model, set head with static html, body with dynamic editable components
  /**
   * Коли іфрейм завантажено створює в ньому {@link PreviewPanelComponent}, додає стилі і потрібні скрипти
   * @param frame iframe з шаблону
   */
  onFrameLoad(frame: HTMLIFrameElement): void {
    this.applicationState = {previewFrame: frame};
    setTimeout(() => {
      this.frameDocument = frame.contentDocument;
      const compFactory = this.componentFactoryResolver.resolveComponentFactory(PreviewPanelComponent);
      const compRef = this.viewContainerRef.createComponent(compFactory);
      this.frameDocument.body.appendChild(compRef.location.nativeElement);

      const styleWrapper = document.createElement('div');
      styleWrapper.innerHTML = previewStyles.trim();
      this.frameDocument.head.appendChild(styleWrapper.firstChild);

      const loadScript = (src) => {
        const node = document.createElement('script');
        node.src = src;
        node.type = 'text/javascript';
        node.async = true;
        node.charset = 'utf-8';
        this.frameDocument.head.appendChild(node);
      };
      loadScript('https://cdn.ampproject.org/v0/amp-carousel-0.1.js');
      loadScript('https://cdn.ampproject.org/v0/amp-accordion-0.1.js');
      loadScript('https://cdn.ampproject.org/v0.js');
      // loadScript('assets/BannerApp.js');
    });
  }

}
