import {Input, Output, EventEmitter, OnInit, DoCheck, AfterViewChecked} from '@angular/core';
import {UINodeComponent} from '../../ui-node-component/ui-node.component';
import {ControlUpdatedEvent, InputObject, UINodeEventType} from '../../../../entities/UIObject';
import {isEqual} from 'lodash';

/**
 *  Базовий компонент відображення інпутів
 */
export class BaseInputComponent extends UINodeComponent implements OnInit, DoCheck, AfterViewChecked {
  node: InputObject;
  private initiallySet = false;
  protected _value: any;

  @Output() valueChange = new EventEmitter();
  uiNodeEvent: EventEmitter<ControlUpdatedEvent> = new EventEmitter<ControlUpdatedEvent>();


  @Input() get value() {
    return this._value;
  }

  set value(newValue) {
    console.log('Base input component set value');
    if (newValue === this._value) {
      this.initiallySet = true;
      return;
    }
    this._value = newValue;
    if (this.initiallySet) {
      this.emitChangeEvent();
    }
    this.initiallySet = true;
  }

  ngOnInit() {
    super.ngOnInit();
    this.setInitialValue();
  }

  protected updateLocalState() {
    super.updateLocalState();
    if ((this.node.localState && this.node.localState.initialValue)) {
      this.node.value = this.node.localState.initialValue;
      this._value = this.node.value;
    }
  }

  ngDoCheck(): void {
    if (this.node && this.container && this.container.node && !isEqual(this.node, this.container.node)) {
      this.node = (<InputObject>this.container.node);
    }
    if (this.node && !isEqual(this.node.localState, this.localState)) {
      this.updateLocalState();
    }
  }

  ngAfterViewChecked(): void {
    if (this.node && this.node.value !== this._value) {
      this._value = this.node.value;
      this.cdr.detectChanges();
    }
  }

  protected setInitialValue() {
    if (this.node) {
      this.value = this.node.value;
    }
  }

  protected emitChangeEvent() {
    this.valueChange.emit(this._value);
    if (this.node) {
      this.uiNodeEvent.emit({
        type: UINodeEventType.CONTROL_VALUE_UPDATE,
        emitter: {
          ...this.node,
          value: this.value
        }
      });
    }
  }

}
