import {distinctUntilChanged, takeUntil} from 'rxjs/operators';
import {destroyEmitter, StateStreamOptions} from '../decorator.types';
import DecoratorTools from '../decorator.tools';
import {EditorEventType} from '../../../entities/EditorEvent';
import {EventsService} from '../../../state/events.service';
import {StateManagerService} from '../../../state/state-manager.service';

export function StateStream(options: StateStreamOptions = {}) {
    return function (target: Object, key: string) {
        function getStream(/*context*/) {
            return StateManagerService
                .getStatePropertyStream(options.stateProperty)
                .pipe(
                    // takeUntil(context[destroyEmitter]),
                    distinctUntilChanged()
                    //     filter(v => !!v)
                );
        }

        // DecoratorTools.redefineAccessors(target, key, (_get, _set) => {
        //   return {
        //     get: function () {
        //       let stream = _get.call(this);
        //       if (!stream) {
        //         stream = getStream(this);
        //         _set.call(this, stream);
        //       }
        //       return stream;
        //     },
        //     set: function (value: any, doNotEmit: boolean) {
        //       if (doNotEmit) {
        //         _set.call(this, value);
        //       } else {
        //         EventsService.dispatch({
        //           type: EditorEventType.UPDATE_APP_STATE,
        //           data: value
        //         });
        //       }
        //     }
        //   };
        // });

        let stream;
        DecoratorTools.redefineAccessors(target, key, (_get, _set) => {
            return {
                get: function () {
                    if (!stream) {
                        stream = getStream();
                    }
                    return stream;
                },
                set: function (value: any, doNotEmit: boolean) {
                    if (doNotEmit) {
                        stream = value;
                    } else {
                        EventsService.dispatch({
                            type: EditorEventType.UPDATE_APP_STATE,
                            data: value
                        });
                    }
                }
            };
        });
    };
}
