import {ChangeDetectorRef, Component, ElementRef, HostBinding, Injector} from '@angular/core';
import {UINodeComponent} from '../ui-node-component/ui-node.component';
import {EventsService} from '../../../state/events.service';
import {filter} from 'rxjs/operators';
import {EditorEvent} from '../../../entities/EditorEvent';
import {HtmlNodeType} from '../../../../../../ui-editor-common/entities/HtmlObject';

/**
 * Компонет відображення ui thumbnail потомок {@link UINodeComponent}
 */
@Component({
    selector: 'block-thumb-component',
    templateUrl: './block-thumb.component.html',
    styleUrls: ['./block-thumb.component.less']
})
export class BlockThumbComponent extends UINodeComponent {
    @HostBinding('attr.draggable') draggable = true;

    public constructor(el: ElementRef,
                       cdr: ChangeDetectorRef,
                       events: EventsService,
                       injector: Injector) {
        super(cdr, el, events, injector);
    }

    // protected isOverCurrentNode() {
    //   return filter((e: EditorEvent) => e.data.target === this.el.nativeElement || this.el.nativeElement.contains(e.data.target));
    // }

    protected isOverCurrentNode(e: EditorEvent): boolean {
        return e.data.target === this.el.nativeElement || this.el.nativeElement.contains(e.data.target);
    }

    protected onDragEnter(e: EditorEvent) {
    }

    protected onDrop(e: EditorEvent): void {
    }

    protected onDragStart() {
        if (this.localState.customBlock || this.localState.externalBlock) {
            this.applicationState = {draggedNode: this.localState.block};
        } else {
            this.applicationState = {
                draggedNode: {
                    type: HtmlNodeType.ELEMENT_NODE,
                    children: [{
                        type: HtmlNodeType.TEXT_NODE,
                        children: [],
                        path: null,
                        tag: null,
                        content: this.localState.block.tag,
                        attributes: {}
                    }],
                    path: null,
                    tag: this.localState.block.tag,
                    attributes: {style: 'padding:10px; border: 1px solid green; background-color: lightgray;'},
                }
            };
        }
        super.onDragStart();
    }
}


