import {
  ReducerOptions,
  StateStreamOptions,
  StateSubscriberOptions,
  EventSubscriberOptions,
  SubscriberType,
  SubscriberOptions
} from '../decorator.types';

function addSubscriber(target: any, prop: string, key: string, options: SubscriberOptions): void {
  let subscribersProp = Object.getOwnPropertyDescriptor(target, prop);
  if (!subscribersProp) {
    subscribersProp = Object.defineProperty(target, prop, {
      value: [
        ...(target.constructor.prototype[prop] || [])
      ]
    });
  }
  target[prop].push({key, className: target.constructor.name, ...options});
}

export function EventSubscriber(options?: EventSubscriberOptions) {
  return function (target: any, key: string, descriptor: PropertyDescriptor) {
    addSubscriber(target, SubscriberType.subscriber, key, options);
  };
}

export function Reducer(options?: ReducerOptions) {
  return function (target: any, key: string, descriptor: PropertyDescriptor) {
    addSubscriber(target, SubscriberType.reducer, key, options);
  };
}

export function StateSubscriber(options?: StateSubscriberOptions) {
  return function (target: any, key: string, descriptor: PropertyDescriptor) {
    addSubscriber(target, SubscriberType.stateSubscriber, key, options);
  };
}
