import {EditorEvent, EditorEventType} from '../entities/EditorEvent';
import {ReplaySubject} from 'rxjs';
import {LogMethod} from '../tools/decorators/Log/Log.decorator';
import {filter} from 'rxjs/operators';
import {SharedSubscription} from '../tools/SharedSubscription';
import {EventSubscriberOptions, SubscriberType} from '../tools/decorators/decorator.types';


export class EventsService {
    private static dispatcher$ = new ReplaySubject<EditorEvent>(1);
    private static subscribers = new SharedSubscription<EventSubscriberOptions>(
        EventsService.dispatcher$,
        EventsService.isTargetSubscriber,
        EventsService.isInvalidSubscriber
    );

    public constructor() {
        window['EventsService'] = EventsService;
    }

    private static isTargetSubscriber(subscriber: EventSubscriberOptions, event: EditorEvent): boolean {
        if (event.type === subscriber.type) {
            return !subscriber.pipes || subscriber.context[subscriber.pipes].call(subscriber.context, event)
        }
        return false;
    }

    private static isInvalidSubscriber(subscriber: EventSubscriberOptions, event: EditorEvent): boolean {
        return subscriber.context.destroyed || (subscriber.condition && !subscriber.context[subscriber.condition]);
    }

    public static addSubscriber(subscriber: EventSubscriberOptions): Function {
        return this.subscribers.addSubscriber({
            options: subscriber,
            invoke: e => subscriber.context[subscriber.key].call(subscriber.context, e)
        });
    }

    public static getEventStream(type: EditorEventType) {
        return EventsService.dispatcher$
            .pipe(
                filter((e: EditorEvent) => e.type === type)
            );
    }

    // @LogMethod()
    public static dispatch(event: EditorEvent): void {
        EventsService.dispatcher$.next(event);
    }

    public static getDispatcher(): ReplaySubject<EditorEvent> {
        return EventsService.dispatcher$;
    }
}
