import {HtmlAttributes, HtmlNodeType, HtmlObject} from '../../../common/entities/HtmlObject';


export class HtmlConverter {

  private static getDomElementAttributes(domElement: Element): HtmlAttributes {
    const attributes = domElement.attributes || [];
    const result: HtmlAttributes = {};
    for (let i = 0; i < attributes.length; i++) {
      // result[attributes[i].name] = attributes[i].value;
      //todo may cause problems in the future
      const attributeName = attributes[i].name.replace(/(\-\w)/g, m => m[1].toUpperCase());
      result[attributeName] = attributes[i].value;
    }
    return result;
  }

  private static setElementAttributes(e: Element, attributes: HtmlAttributes = {}): Element {
    Object.keys(attributes).forEach(attribute => {
      try {
        //todo may cause problems in the future
        const attributeName = attribute.replace( /([a-z])([A-Z])/g, '$1-$2' ).toLowerCase();
        e.setAttribute(attributeName, attributes[attribute]);
        // e.setAttribute(attribute, attributes[attribute]);
      } catch (e) {
        console.warn('wrong attribute', attribute, attributes[attribute]);
      }
    });
    return e;
  }

  private static stringToDom(html: string): Element[] {
    const template = document.createElement('template');
    html = html.trim();
    template.innerHTML = html;
    return Array.from(template.content.childNodes) as Element[];
  }

  public static parseDom(domElement: Element, path: number[] = [], removePathAttribute?: boolean): HtmlObject {
    const object: HtmlObject = {} as HtmlObject;
    object.tag = domElement.tagName;
    object.type = domElement.nodeType;
    if (object.type === HtmlNodeType.TEXT_NODE || object.type === HtmlNodeType.COMMENT_NODE) {
      object.content = domElement.textContent;
    }
    object.attributes = HtmlConverter.getDomElementAttributes(domElement);
    if (removePathAttribute) {
      delete object.attributes.path;
    }
    object.children = [];
    object.path = path;
    const children = domElement.childNodes || [];
    for (let i = 0; i < children.length; i++) {
      object.children.push(HtmlConverter.parseDom(children[i] as Element, [...path, i]));
    }
    return object;
  }

  public static parseObject(object: HtmlObject, storePathToAttribute?: boolean): Element | Comment | Text {
    if (object.type === HtmlNodeType.COMMENT_NODE) {
      return document.createComment(object.content);
    }
    if (object.type === HtmlNodeType.TEXT_NODE) {
      return document.createTextNode(object.content);
    }

    const element = document.createElement(object.tag);
    HtmlConverter.setElementAttributes(element, storePathToAttribute ? {
      ...object.attributes,
      path: object.path.join(',')
    } : object.attributes);

    (object.children || []).forEach(child => {
      element.appendChild(HtmlConverter.parseObject(child, storePathToAttribute));
    });

    return element;

  }

  public static HtmlToObject(html: string, path: number[] = []): HtmlObject[] {
    const els = HtmlConverter.stringToDom(html);
    return els.map((el, i) => HtmlConverter.parseDom(el, path.length ? [...path.slice(0, -1), path[path.length - 1] + i] : path));
  }

  public static ObjectToHtml(object: any): string {
    return (HtmlConverter.parseObject(object) as Element).outerHTML;
  }

  public static stylesToObject(styles = '') {
    const stylesObj = {};
    styles.split(';').forEach(style => {
      if (style.trim()) {
        const [prop, value] = style.split(':');
        stylesObj[prop.trim()] = value.trim();
      }
    });
    return stylesObj;
  }

}

window['HtmlConverter'] = HtmlConverter;
