import {Pipe, PipeTransform} from '@angular/core';
import {ControlObject} from '../../../entities/UIObject';
import {cloneDeep} from 'lodash';

@Pipe({
  name: 'controlObjectValue'
})
export class ControlObjectValuePipe implements PipeTransform {

  constructor() {
  }

  private cleanDevProps(value): void {
    if (value) {
      delete value.type;
      delete value.path;
      delete value._id;
      delete value.customBlockId;
      delete value.documentId;
      delete value.__v;
      (value.children || []).forEach(c => this.cleanDevProps(c));
    }
  }

  public transform(value: ControlObject): ControlObject {
    if (!value) {
      return {} as any;
    }
    value = cloneDeep(value);
    this.cleanDevProps(value);
    return value;
  }
}
