import {HtmlNodeType, HtmlObject} from '../../../../../../../ui-editor-common/entities/HtmlObject';
import {BaseValueAccessor} from './BaseValueAccessor';
import {HtmlConverter} from '../../../../tools/HtmlConverter';
import {NodeUtils} from '../../../../../../../ui-editor-common/tools/NodeUtils';
import {IterableControlValueAccessor, IterableValueItem} from '../../../../entities/UIObject';
import {Path} from '../../../../../../../ui-editor-common/entities/Path';

export class DomNodeAccessor extends BaseValueAccessor implements IterableControlValueAccessor {
  private modifiedPath: Path;
  protected node;

  public structural = true;

  constructor(node: HtmlObject) {
    super();
    this.node = node;
  }

  protected getDomNode() {
    return HtmlConverter.parseObject(this.node, true);
  }

  protected getNodeByDomNode(domNode: Element): HtmlObject {
    const fullPath = domNode
      .getAttribute('path')
      .split(',')
      .map(e => parseInt(e, 10));

    const relativePath = fullPath.slice(this.node.path.length);
    return NodeUtils.getChildByPath(this.node, relativePath);
  }

  protected getIterableItems(): HtmlObject[] {
    return this.node.children.filter(c => c.type === HtmlNodeType.ELEMENT_NODE);
  }

  getRawValue(): HtmlObject {
    return this.node;
  }

  getValue(valuePath: string): HtmlObject {
    if (valuePath) {
      const domNode = (<Element>this.getDomNode()).querySelector(valuePath);
      if (domNode) {
        return this.getNodeByDomNode(domNode);
      }
      return null;
    }
    return this.node;
  }

  setValue(valuePath: string, value: HtmlObject): void {
    if (valuePath) {
      const domNode = (<Element>this.getDomNode()).querySelector(valuePath);
      if (domNode) {
        const oldNode = this.getNodeByDomNode(domNode);
        value.path = oldNode.path;
        NodeUtils.reIndexNodeChildren(value);
        Object.assign(oldNode, value);
        this.modifiedPath = oldNode.path;
      }

      return;
    }

    value.path = this.node.path;
    NodeUtils.reIndexNodeChildren(value);
    this.node = value;
    this.modifiedPath = this.node.path;
  }

  [Symbol.iterator](): Iterator<IterableValueItem> {
    let pointer = 0;
    const iterableItems = this.getIterableItems();
    return {
      next(value?: any): IteratorResult<any> {
        if (pointer < iterableItems.length) {
          const result = {
            done: false,
            value: {
              key: pointer,
              value: iterableItems[pointer]
            } as IterableValueItem
          };
          pointer++;
          return result;
        } else {
          return {
            done: true,
            value: null
          };
        }
      }
    };
  }

  // todo maybe should be moved to another accessor
  addItem(item: HtmlObject): any {
    item.tag = item.tag.toUpperCase();
    this.node.children.push({
      attributes: {},
      children: [],
      path: [],
      type: HtmlNodeType.ELEMENT_NODE,
      ...item
    });
    NodeUtils.reIndexNodeChildren(this.node);
    this.modifiedPath = this.node.children[this.node.children.length - 1].path;
  }

  getItem(valuePath: number): any {
    return this.node.children[valuePath];
  }

  removeItem(valuePath: number): any {
    const iterableItems = this.getIterableItems();
    this.modifiedPath = iterableItems[valuePath].path;
    this.node.children = this.node.children.filter(c => c !== iterableItems[valuePath]);
    NodeUtils.reIndexNodeChildren(this.node);
  }

  updateItem(valuePath: number, value: HtmlObject): any {
    const iterableItems = this.getIterableItems();
    const oldItem = iterableItems[valuePath];
    value.path = oldItem.path;
    NodeUtils.reIndexNodeChildren(value);
    Object.assign(oldItem, value);
    this.modifiedPath = oldItem.path;
  }

  public getModifiedPath(): Path {
    return this.modifiedPath;
  }
}
