import {ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Injector, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {EventsService} from '../../../state/events.service';
import {
  ControlObject,
  ControlType,
  ControlValueAccessorType,
  SettingsObject,
  UINodeEventType,
  UIRendererMode
} from '../../../entities/UIObject';
import {HtmlObject} from '../../../../../../ui-editor-common/entities/HtmlObject';
import {ControlPanelComponent} from '../control-panel/control-panel.component';
import {ActionManagerService} from '../../../domain/action-manager.service';
import {ActionsService} from '../../../domain/actions.service';
import {cloneDeep} from 'lodash';
import {NodeWrapperService} from '../../../services/node-wrapper/node-wrapper.service';
import {ControlsService} from '../../../services/controls/controls.service';
import {StateSubscriber} from '../../../tools/decorators/Subscriber/Subscriber.decorator';
import {EditorEventListener} from '../../../tools/decorators/EditorEventListener/EditorEventListener.decorator';
import {ControlUtils} from '../../../tools/ControlUtils';
import {BehaviorableNode} from '../../../entities/BehaviorableNode';
import {DomDataService} from '../../../../../../ui-editor-common/services/dom.data.service';

/**
 * Контейнер редагування/додавання нових контролів в панелі налаштуваннь. Потомок {@link ControlPanelComponent}
 * мапиться на відображення по типу CONTROL_EDITOR_PANEL {@link UIObjectType}
 * */
@Component({
  selector: 'control-editor-panel',
  templateUrl: './control-editor-panel.component.html',
  styleUrls: ['./control-editor-panel.component.less', '../base-nodes-container/base-nodes-container.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
@EditorEventListener()
export class ControlEditorPanelComponent extends ControlPanelComponent implements OnInit, OnDestroy {
  rendererMode = UIRendererMode.EDIT;
  editedNode: HtmlObject;

  public constructor(events: EventsService,
                     actionManager: ActionManagerService,
                     actions: ActionsService,
                     cdr: ChangeDetectorRef,
                     el: ElementRef,
                     private wrapper: NodeWrapperService,
                     private controls: ControlsService,
                     injector: Injector) {
    super(events, actionManager, actions, cdr, el, injector);
  }

  private cleanConfig(node: SettingsObject) {
    delete node.value;
    node.children.forEach(n => this.cleanConfig(n));
  }

  private getCleanControlConfig(): ControlObject {
    const config = cloneDeep(this.applicationState.controlsDataService.data.html);
    this.cleanConfig(config);
    return config;
  }

  @StateSubscriber({stateProperty: 'applicationState.selectedNode', definedOnly: true})
  protected onNodeSelected(node: BehaviorableNode) {
    this.editedNode = node.getHtmlObjectValue();
    if (!this.applicationState.controlsDataService) {
      this.applicationState = {
        controlsDataService: new DomDataService({
          html: this.applicationState.editedControl || {
            ...ControlUtils.defaultControlProperties,
            tag: ControlType.BASE,
            valueAccessor: ControlValueAccessorType.BUFF,
            children: [],
            bindings: {value: 'editedNode'}
          }
        } as any)
      };
    } else {
      this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
    }
  }

  public exit(): void {
    this.applicationState = {
      addingControl: false,
      controlsDataService: undefined,
      editedControl: undefined,
    };
    this.uiNodeEvent.emit({type: UINodeEventType.CONTROL_PROPERTY_UPDATED});
  }

  public save(): void {
    this.controls.saveControl(this.getCleanControlConfig(), this.editedNode.customBlockId);
    this.wrapper.selectNode(this.editedNode);
    this.exit();
  }
}
